/* ------------------------------------------------------------ *\
	Discount
\* ------------------------------------------------------------ */

.discount {
	display: flex; 
	align-items: center;
	flex-wrap: wrap;

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 300px;
		margin: 0 auto;
	}

	.discount__content {
		width: 80%; 

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
			margin-bottom: 21px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}
	}

	.discount__aside {
		width: 20%; 
		padding-left: 10px; 
		margin-bottom: 4px; 
		font-size: 14px; 
		text-align: right;

		/*Mobile*/
		 @include breakpoint-mobile {
		 	width: 100%; 
		 	padding-left: 0; 
		 	margin-bottom: 0;
		 	font-size: 13px;  
		 	text-align: center; 
		 } 

		h3 {
			margin-bottom: 13px; 
			font-size: 16px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 8px; 
		 		font-size: 18px; 
		 		line-height: 1.28; 

			 	strong {
			 		display: block; 
			 		margin-bottom: 2px; 
			 		font-size: 22px; 
			 		line-height: 1.05; 
			 	}
			}

			&:last-child {
				margin-bottom: 0; 
			}
		}

		p {
			margin-bottom: 25px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 19px; 
			}

			em {
				margin-left: -2px; 
				font-size: 13px; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-left: 0; 
					font-size: 12px; 
				}
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		.list-price {
			justify-content: flex-end;
			margin-bottom: 6px; 
			font-size: 11px; 

			/*Mobile*/
			@include breakpoint-mobile {
				justify-content: center;
				margin-bottom: 4px; 
				font-size: 10px; 
			}

			strong {
				color: $c-blue-dark;
			}
		}

		.btn {
			min-width: 103px; 
			margin-right: -3px; 
			font-size: 11px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				margin-right: 0; 
			}

			/*Mobile*/
			@include breakpoint-mobile {
				min-width: 140px;
			}

			i {
				margin-left: 2px; 
			}
		}
	}

	.discount__body {
		display: flex; 
		flex-wrap: wrap;
		margin: 0 -50px;

		/*Mobile*/
		@include breakpoint-mobile {
			display: block; 
			margin: 0;
		}
	}

	.discount-item {
		position: relative; 
		width: 33.33%; 
		padding: 0 50px;

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
			padding: 0;
			margin-bottom: 99px; 

			&:last-child {
				margin-bottom: 0; 
			}

			.product {
				display: flex; 
				align-items: flex-start;
				flex-wrap: wrap;
				max-width: 100%;
			}

			.product__image {
				width: 105px; 
				margin-bottom: 0; 
			}

			.product__content {
				display: flex; 
				flex-wrap: wrap;
				width: calc(100% - 105px);
				padding-left: 32px; 
				margin-top: -2px; 

				> * {
					width: 100%; 
				}

				h3 {
					margin-bottom: 5px;

					a {
						display: block; 
					}
				}

				p {
					margin-bottom: 10px;

					&:last-child {
						margin-bottom: 10px; 
					} 
				}

				.select {
					width: calc(50% - 5px); 
					max-width: 100px;
					order: 4;

					+ .select {
						margin-left: 10px; 
					}
				}
			}

			.list-gallery {
				display: none; 
			}

			.list-rating {
				display: none; 
			}

			.list-price {
				margin-bottom: 3px !important; 
			}
		}

		.ico-plus-circle {
			position: absolute; 
			right: 0; 
			top: calc(50% + 29px);
			z-index: 5;
			transform: translate(50%, -50%); 

			/*Mobile*/
			@include breakpoint-mobile {
				top: calc(100% + 49px);
				right: auto; 
				left: 50%; 
				transform: translateX(-50%);
			}
		}

	}

	.discount-item__title {
		position: absolute; 
		top: 0; 
		left: 100%;
	    transform: rotate(-90deg) translate(-149px, -193px);
	    width: 380px;
		font-size: 60px; 
	    color: #e6e6e6;
	    font-weight: bold;
	    text-transform: uppercase;
	    text-align: center;

	    /*Mobile*/
	    @include breakpoint-mobile {
	    	top: calc(100% + 27px);
	    	right: 0; 
	    	left: -4px; 
	    	transform: none;
	    	width: auto; 
	    	font-size: 50px; 
	    	letter-spacing: -.01em;
	    	white-space: nowrap;
	    }
	}
}
