/* ------------------------------------------------------------ *\
	Widgets
\* ------------------------------------------------------------ */

.widgets {
	list-style: none outside none;

	.widgets__close {
		/*Mobile*/
		@include breakpoint-mobile {
			position: absolute; 
			top: 30px; 
			right: 15px; 
			z-index: 15;
		}
	}
}

/* ------------------------------------------------------------ *\
	Widget
\* ------------------------------------------------------------ */

.widget {
	margin-bottom: 46px; 
	color: $c-grey-dark;

	&:last-child {
		margin-bottom: 0; 
	}

	.widget__title {
		margin-bottom: 23px; 
		font-size: 16px; 

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-links-tertiary {
		margin-bottom: 23px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 15px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	&--gutter-md {
		margin-bottom: 41px; 
	}
}
