/* ------------------------------------------------------------ *\
	Product Cart
\* ------------------------------------------------------------ */

.product-cart {
	position: relative; 
	display: flex; 
	flex-wrap: wrap;
	padding-right: 68px; 
	margin-bottom: 32px; 

	/*Mobile*/
	@include breakpoint-mobile {
		@include clearfix;
		display: block; 
		padding-right: 40px; 
		margin-bottom: 25px; 
	}

	&:last-child {
		margin-bottom: 0; 
	}

	p {
		em {
			font-size: 11px; 
		}
	}

	.product__content {
		width: calc(100% - 180px); 
		padding: 0 10px;

		/*Mobile*/
		@include breakpoint-mobile {
			float: left; 
			width: calc(100% - 75px); 
			margin-bottom: 19px; 
		}

		h4 {
			min-height: 28px;
			margin-bottom: 2px; 

			/*Mobile*/
			@include breakpoint-mobile {
				min-height: auto;
				margin-bottom: 9px; 
			}

			a {
				&:hover {
					text-decoration: none; 
				}
			}
		}

		> p {
			display: none; 
		}
	}

	.product__image {
		transform: translateY(2px);
		width: 75px;  

		/*Mobile*/
		@include breakpoint-mobile {
			float: left; 
			transform: translateY(0); 
		}

		a {
			&:hover {
				opacity: .8; 
			}
		}
	}

	.product__aside {
		width: 105px; 
		align-self: center;

		/*Mobile*/
		@include breakpoint-mobile {
			float: left; 
			padding-left: 8px; 
		}
	}

	.product__remove {
		position: absolute; 
		top: 50%;
		right: 0; 
		transform: translateY(-50%); 
		padding: 8px;
		margin-top: -2px; 

		/*Mobile*/
		@include breakpoint-mobile {
			top: -5px; 
			right: 12px; 
			transform: translateY(0); 
		}

		i {
			display: block; 
		}

		&:hover {
			opacity: .8; 
		}
	}

	.list-price {
		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 -2px;

			li {
				padding: 0 2px;

				&:after {
					display: none; 
				}

				&:first-child {
					order: 2;
				}
			}

			strong {
				color: $c-blue-dark;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Products
\* ------------------------------------------------------------ */

.products {
	display: flex; 
	flex-wrap: wrap;
	margin: 0 -50px;

	/*Mobile*/
	@include breakpoint-mobile {
		display: block; 
		margin: 0;
	}

	.product,
	.banner-secondary {
		width: 33.33%; 
		padding: 0 50px;

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
			padding: 0; 
			margin-bottom: 45px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		&:nth-child(n+4) {
			margin-top: 37px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-top: 0; 
			}
		}
	}

	.banner-secondary {
		width: 66.67%; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
			width: 100%; 
		}
	}
}

/* ------------------------------------------------------------ *\
	Product
\* ------------------------------------------------------------ */

.product {
	width: 100%; 

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 350px;
		margin-right: auto; 
		margin-left: auto; 
	}

	.product__content {
		position: relative; 

		h3 {
			margin-bottom: 7px; 
			font-family: $f-proxima-nova;
			font-size: 12px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 5px; 
				font-family: $f-f37bolton;
			}

			a {
				&:hover {
					text-decoration: none; 
				}
			}
		}

		p {
			margin-bottom: 7px; 

			em {
				font-size: 11px; 
			}

			span {
				color: $c-grey-dark;

				/*Mobile*/
				@include breakpoint-mobile {
					color: $c-blue-dark;
				}
			}

			&:last-child {
				margin-bottom: 0; 
			}
		}

		.list-price {
			margin-bottom: 1px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 0; 
			}
		}

		.btn-text {
			display: inline-block; 
			margin-top: 3px; 
			font-size: 10px; 
			color: $c-red;

			/*Mobile*/
			@include breakpoint-mobile {
				right: 0; 
				bottom: 2px; 
				height: 31px; 
				padding: 0 15px;
				border: 2px solid #ae1232;
				border-radius: 15px; 
				background: #ae1232;
				font-size: 12px; 
				line-height: 28px; 
				color: $c-white;
				text-align: center; 
			}
			
			&:first-child {
				margin-top: 0; 
			}

			i {
				/*Mobile*/
				@include breakpoint-mobile {
					margin-left: 3px; 
				}
			}
		}
	}

	.product__image {
		position: relative; 
		display: block; 
		margin-bottom: 6px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 10px; 

			img {
				width: 100%; 
			}
		}

		&:after {
			@include full;
			content: ''; 
			background: rgba(0,0,0,.04);
			transition: opacity $t-duration, visibility $t-duration;
			cursor: pointer;
			pointer-events: none;
		}

		&:hover {
			&:after {
				opacity: 0; 
				visibility: hidden; 
			}
		}

		.ico-checkbox-checked {
			position: absolute; 
			top: 0; 
			left: 0; 
		}
	}

	.product__wishlist {
		position: absolute; 
		top: 9px; 
		left: 8px; 
		text-decoration: none; 

		i {
			vertical-align: middle; 
		}

		span {
			display: inline-block; 
			border-radius: 10px; 
			padding: 0 14px 3px;
			margin-left: 2px; 
			background: $c-blue-dark;
			font-size: 10px; 
			color: $c-white;
			font-weight: bold; 
			display: none; 
		}

		&:hover {
			text-decoration: none; 

			i {
				background-image: url(../images/heart-dark@2x.png);

				/*Mobile*/
				@include breakpoint-mobile {
					background-image: url(../images/heart@2x.png);
				}
			}

			span {
				display: inline-block; 

				/*Mobile*/
				@include breakpoint-mobile {
					display: none; 
				}
			}
		}

		&.active {
			i {
				background-image: url(../images/heart-dark@2x.png);
			}

			span {
				display: none; 
			}
		}

		&--secondary {
			top: 16px; 
			right: 16px; 
			left: auto; 
			display: flex; 

			/*Mobile*/
			@include breakpoint-mobile {
				top: 14px; 
				right: auto; 
				left: 9px; 
			}

			i {
				order: 2;
			}

			span {
				margin-top: -2px; 
				margin-right: 6px; 
				margin-left: 0; 
				opacity: 1; 
			}
		}
	}

	.product__discount {
		position: absolute; 
		top: 15px; 
		right: 10px; 
		font-size: 12px; 
		line-height: 1.2; 
		font-weight: bold; 
		color: $c-white;

		ul {
			text-align: right; 
			list-style: none; 
		}

		li {
			margin-bottom: 5px; 
			
			&:last-child {
				margin-bottom: 0; 
			}

			span {
				display: inline-block; 
				min-width: 39px;
				padding: 0 3px 1px;
				border-radius: 8px; 
				background: #ae1232;
				text-align: center; 
			}
		}

		&--secondary {
			top: 10px; 
		}
	}

	.select {
		margin-bottom: 8px; 

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-gallery {
		margin-top: -2px; 
		margin-bottom: 8px; 
	}

	.list-rating {
		margin-top: -4px; 
		margin-bottom: 3px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-top: -1px; 
			margin-bottom: 5px; 
		}
	}

	&--secondary {
		min-height: 323px;

		.product__image {
			margin-bottom: 13px; 
		}

		.product__content {
			p {
				margin-bottom: 5px; 
			}

			.list-gallery {
				margin-top: -9px; 
				margin-bottom: 7px; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-top: 0; 
					margin-bottom: 4px; 
				}
			}

			.btn-text {
				position: absolute; 
				right: 2px; 
				bottom: -5px; 
			}

			.list-price {
				margin-bottom: 0; 
			}
		}

		.product__wishlist {
			top: 8px; 
			left: 11px; 

			/*Mobile*/
			@include breakpoint-mobile {
				top: 14px; 
				left: 9px; 
			}
		}
	}

	&--tertiary {
		.product__image {
			margin-bottom: 9px; 
		}

		.product__content {
			h3 {
				margin-bottom: 9px; 
			}

			.list-gallery {
				margin-top: 0px;
				margin-bottom: 11px;  
			}

			.list-rating {
				margin-bottom: 6px; 
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Product Single
\* ------------------------------------------------------------ */

.product-single {
	display: flex; 
	flex-wrap: wrap;

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 400px;
		margin: 0 auto;
	}

	h6 {
		margin-bottom: 7px; 
		text-transform: none; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: flex; 
			justify-content: space-between;
			flex-wrap: wrap;
			width: 100%; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.product__title {
		margin-bottom: 2px; 
		font-size: 20px; 
		line-height: 1.2; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 5px; 
			font-size: 18px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.product__content {
		width: 305px; 
		padding-right: 20px; 
		font-size: 14px; 

		/*Mobile*/
		@include breakpoint-mobile {
			position: relative; 
			order: 2;
			width: 100%; 
			padding-right: 0; 
			line-height: 1.36; 

			.list-rating {
				position: absolute; 
				top: 9px; 
				right: -2px; 
			}
		}

		p {
			margin-bottom: 10px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 17px; 
			}

			&:last-child {
				margin-bottom: 0; 
			}

			em {
				font-size: 13px; 
				margin-left: -2px; 
			}
		}
	}

	.product__image {
		position: relative; 
		display: flex; 
		justify-content: space-between;
		flex-wrap: wrap;
		width: calc(100% - 305px); 

		/*Mobile*/
		@include breakpoint-mobile {
			order: 1;
			width: 100%; 
			margin-bottom: 12px; 

			> * {
				width: 100%; 
			}

			.breadcrumbs {
				order: 1;

				&.mobile-clone {
					display: block !important; 
				}
			}

			.product__title {
				order: 2;

				&.mobile-clone {
					display: block !important; 
				}
			}

			.list-text {
				order: 3;

				&.mobile-clone {
					display: flex !important; 
				}
			}
		}

		&.active {
			.product__image-zoom {
				z-index: 10;
			}

			.product__zoom-alt {
				display: inline-block; 
			}
		}

		&-zoom {
			position: absolute; 
			top: -33px; 
			right: -127px; 
			bottom: 0; 
			left: -55px; 

			@media (max-width: 1280px) {
				right: calc(-50vw + 510px); 
			}

			/*Mobile*/
			@include breakpoint-mobile {
				top: 0; 
				right: -15px; 
				bottom: -157px; 
				left: -15px; 
				width: auto; 
			}
		}

		&-main {
			position: relative; 
			z-index: 5;
			width: calc(64.2% - 51px); 

			/*Mobile*/
			@include breakpoint-mobile {
				order: 4;
				width: 100%; 
			}
		}

		&-title {
			width: 102px;

			/*Mobile*/
			@include breakpoint-mobile {
				display: none; 
			}

			h3 {
				transform: rotate(-90deg) translateX(-325px);
				max-width: 420px;
				transform-origin: 50%;
				font-size: 90px; 
				color: $c-grey;
				font-weight: bold; 
				text-transform: uppercase; 
				text-align: center; 
			} 
		}

		&-aside {
			width: calc(35.8% - 51px); 

			/*Mobile*/
			@include breakpoint-mobile {
				display: none; 
			}

			img {
				margin-bottom: 15px; 

				&:last-child {
					margin-bottom: 0; 
				}
			}
		}
	}

	.product__zoom {
		position: absolute; 
		right: 11px; 
		bottom: 11px; 
		display: inline-block; 
		font-size: 10px; 
		text-align: center; 

		/*Mobile*/
		@include breakpoint-mobile {
			right: 7px; 
		}

		i {
			margin-bottom: 5px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		strong,
		span {
			display: block; 
		}

		&-alt {
			position: absolute; 
			display: none; 
			top: calc(50% - 11px);
			right: auto; 
			bottom: auto; 
			left: 37px; 
			transform: translateY(-50%); 
			z-index: 15;
			font-size: 10px; 
			text-align: center; 

			/*Mobile*/
			@include breakpoint-mobile {
				top: auto; 
				right: 25px; 
				bottom: 17px; 
				left: auto; 

				transform: none;
			}

			i {
				margin-bottom: 2px; 

				&:last-child {
					margin-bottom: 0; 
				}
			}

			strong,
			span {
				display: block; 
			}
		}
	}

	.product__discount {
		position: absolute; 
		top: 9px; 
		right: 10px; 
		z-index: 5;
		font-size: 12px; 
		line-height: 1.2; 
		font-weight: bold; 
		color: $c-white;

		ul {
			text-align: right; 
			list-style: none; 
		}

		li {
			margin-bottom: 5px; 
			
			&:last-child {
				margin-bottom: 0; 
			}

			span {
				display: inline-block; 
				min-width: 39px;
				padding: 0 3px 1px;
				border-radius: 8px; 
				background: #ae1232;
				text-align: center; 
			}
		}
	}

	.product__wishlist {
		position: absolute; 
		top: 8px; 
		left: 11px; 
		z-index: 5; 
		text-decoration: none; 

		i {
			vertical-align: middle; 

			/*Mobile*/
			@include breakpoint-mobile {
				&.ico-heart {
					width: 14px; 
					height: 11px; 
				}
			}
		}

		span {
			display: none; 
			border-radius: 10px; 
			padding: 0 14px 3px;
			margin-left: 2px; 
			background: $c-blue-dark;
			font-size: 10px; 
			color: $c-white;
			font-weight: bold; 
		}

		&:hover {
			text-decoration: none; 

			i {
				background-image: url(../images/heart-dark@2x.png);

				/*Mobile*/
				@include breakpoint-mobile {
					background-image: url(../images/heart@2x.png);
				}
			}

			span {
				display: inline-block; 

				/*Mobile*/
				@include breakpoint-mobile {
					display: none; 
				}
			}
		}

		&.active {
			i {
				background-image: url(../images/heart-dark@2x.png);
			}

			span {
				display: none; 
			}
		}
	}

	.breadcrumbs {
		margin-top: -6px; 
		margin-bottom: 5px; 
		margin-left: 1px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-top: 0; 
			margin-left: 0; 
			margin-bottom: 2px; 

			ul {
				justify-content: flex-start;
			}
		}
	}

	.list-text {
		margin-bottom: 11px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 10px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-rating {
		margin-bottom: 8px; 

		/*Mobile*/
		@include breakpoint-mobile {
			.ico-star {
				width: 11px; 
				height: 11px; 
			}
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-price {
		margin-bottom: 4px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 0; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-gallery {
		max-width: 208px;
		padding: 0 10px;
		margin-left: 0; 
		margin-bottom: 18px; 

		/*Mobile*/
		@include breakpoint-mobile {
			max-width: 290px;
			margin: 0 0 20px;
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.list-size {
		padding-top: 3px; 
		margin-bottom: 4px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding-top: 11px; 
			margin-bottom: 26px; 
		}
	} 

	.link {
		margin-bottom: 39px; 

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.btn {
		min-width: 120px;
		padding: 0 13px;
		font-size: 11px; 

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
		}
	}
}
