/* ------------------------------------------------------------ *\
	Table
\* ------------------------------------------------------------ */

.table {
	line-height: 1.17; 

	tr {
		&:nth-child(2n-1) {
			background: $c-grey;
		}
	}

	td {
		padding: 19px 10px 16px;
	}

	span {
		color: $c-grey-dark;
	}

	&--delivery {
		max-width: 400px;
		padding-bottom: 3px; 

		td {
			&:first-child {
				width: 43%; 
				padding-right: 0px; 
				padding-left: 20px; 
			}

			strong {
				text-transform: uppercase; 
			}
		}
	}
}
