/* ------------------------------------------------------------ *\
	Subscribe
\* ------------------------------------------------------------ */

.subscribe {
	/*Mobile*/
	@include breakpoint-mobile {
		padding-left: 5px; 
		max-width: 400px;
		margin-right: auto; 
		margin-left: auto; 
	}

	form {
		@include clearfix;
	}

	.subscribe__field {
		width: 306px;
		padding: 7px 0;
		border-width: 0 0 1px;
		border-style: solid; 
		border-color: $c-red; 
		border-radius: 0; 
		margin-right: 30px; 
		background: none;
		font-size: 12px; 
		line-height: 1.2; 
		color: $c-blue-dark;
		font-weight: bold; 
		text-transform: uppercase; 
		appearance: none;

		/*Mobile*/
		@include breakpoint-mobile {
			float: left; 
			width: calc(100% - 100px); 
			padding: 0 0 7px;
			margin-right: 0; 
		}

		&:-webkit-autofill {
			-webkit-text-fill-color: inherit;
			-webkit-box-shadow: 0 0 0 100px $c-grey-light inset;
		}
	}

	.subscribe__btn {
		float: right; 
		min-width: 203px;
		margin-top: 2px; 
		font-size: 11px; 

		/*Mobile*/
		@include breakpoint-mobile {
			min-width: 90px;
			height: 20px; 
			border-width: 1px;
			padding: 0 10px;
			background: $c-red;
			font-size: 10px; 
			line-height: 18px; 
			color: $c-white;

			&:hover {
				background: transparent;
				color: $c-red;
			}
		}
	}
}
