/* ------------------------------------------------------------ *\
	Slick Carousel
\* ------------------------------------------------------------ */

.slick-dots { 
	display: flex; 
	justify-content: center;
	margin-top: 25px; 
	list-style: none; 

	/*Mobile*/
	@include breakpoint-mobile {
		margin-top: 16px; 
	}

	li { 
		margin: 0 2px; 

		button { 
			display: block; 
			width: 6px; 
			height: 6px; 
			border: 0; 
			border-radius: 3px; 
			background: $c-grey-dark; 
			font-size: 0; 
			transition: width $t-duration, background $t-duration; 
		}

		&.slick-active {
			margin: 0 7px;

			button {
				width: 16px; 
			}
		}

		button:hover,
		&.slick-active button { 
			background: #152342;
		}
	}
}
