/* ------------------------------------------------------------ *\
	Footer
\* ------------------------------------------------------------ */

.footer {
	background: $c-blue-dark;
	color: $c-white;

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		color: inherit;
	}

	h5 {
		margin-bottom: 28px; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: inline-block; 
			margin-right: 3px; 
			margin-bottom: 20px; 
		}

		i {
			&:first-child {
				display: inline-block; 
				vertical-align: middle; 
				margin-right: 6px; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-right: 8px; 
				}
			}
		}

		&.gutter-bottom-sm {
			margin-bottom: 10px;
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.footer__cols {
		@include clearfix;
		margin: 0 -15px;

		/*Mobile*/
		@include breakpoint-mobile {
			display: flex; 
			flex-wrap: wrap;
			margin: 0;
		}
	}

	.footer__col {
		float: left; 
		padding: 0 15px;

		&:last-child {
			margin-bottom: 0; 
		}

		&--size1 {
			width: 15.3%;
		}

		&--size2 {
			width: 19.45%;
		}

		&--size3 {
			width: 23.7%;
		}

		&--size4 {
			width: 45.8%;
			margin-top: 34px; 
		}

		&--size5 {
			width: 22.1%;
		}

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
			padding: 0 0 7px;
			border-bottom: 1px solid #444f68; 
			margin-top: 0; 
			margin-bottom: 28px; 

			&:nth-child(5) {
				order: 6;
				margin-bottom: 0; 
			}

			&:nth-child(6) {
				order: 5;
				margin-top: -6px; 
				margin-bottom: 20px; 
			}
		}
	}

	.footer__content {
		padding: 67px 0 28px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 21px 0;
		}
	}
	
	.footer__bar {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		padding: 28px 0 31px;
		border-top: 1px solid $c-white; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 6px 0 20px;
			border-top: 0; 
		}

		&:first-child {
			border-top: 0; 
		}
	}

	.footer__title {
		/*Mobile*/
		@include breakpoint-mobile {
			position: relative; 
			display: block; 
			margin-right: 0; 

			&:after {
				content: ''; 
				position: absolute; 
				top: 5px; 
				right: 2px; 
				transform: rotate(45deg);
				width: 5px; 
				height: 5px; 
				border-width: 0 1px 1px 0;
				border-color: $c-white; 
				border-style: solid;
				transition: transform $t-duration;
			}
		}
	}

	.logo {
		max-width: 58px;
		flex: 0 0 58px;

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}
	}

	.copyrights {
		margin-right: 40px; 
		margin-left: 44px; 
		font-size: 10px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 28px 0 0;
			line-height: 1.4; 
		}

		&:first-child {
			margin-left: 0; 
		}

		&:last-child {
			margin-right: 0; 
		}
	}

	.socials {
		padding-top: 5px; 
		margin-left: auto; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding-top: 0; 
			margin-bottom: 30px; 
		}
	}

	/*Mobile*/
	@include breakpoint-mobile {
		.list-links {
			display: none; 
			margin-bottom: 11px; 
		}

		.list-cards {
			margin-top: 5px; 
			margin-bottom: 9px; 
		}

		.expanded {
			.footer__title {
				&:after {
					transform: rotate(225deg);
				}
			}
		}
	}
}
