/* ------------------------------------------------------------ *\
	List
\* ------------------------------------------------------------ */

[class^="list-"] { 
	list-style: none; 
	margin-bottom: 0; 
}

/* ------------------------------------------------------------ *\
	List Nav
\* ------------------------------------------------------------ */

.list-nav {
	display: flex; 
	flex-wrap: wrap;
	padding: 18px 10px;
	background: $c-white;
	font-size: 12px; 
	color: $c-grey-dark;
	font-weight: bold; 
	text-transform: uppercase; 

	li {
		width: 33.33%; 
		padding: 0 5px;
		text-align: center; 

		&:nth-child(3n-2) {
			text-align: left; 
		}

		&:nth-child(3n) {
			text-align: right; 
		}
	}
}

/* ------------------------------------------------------------ *\
	List Tags
\* ------------------------------------------------------------ */

.list-tags {
	display: flex; 
	flex-wrap: wrap;
	margin: -4px;

	li {
		padding: 4px;
	}
}


/* ------------------------------------------------------------ *\
	List Size
\* ------------------------------------------------------------ */

.list-size {
	display: flex; 
	flex-wrap: wrap;
	margin: 0 -5px;

	/*Mobile*/
	@include breakpoint-mobile {
		margin: 0 -11px;
	}

	li {
		padding: 0 5px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 11px;
		}
	}
}

/* ------------------------------------------------------------ *\
	List Text
\* ------------------------------------------------------------ */

.list-text {
	display: flex; 
	flex-wrap: wrap;
	font-size: 11px; 

	li {
		&:after {
			content: '•'; 
			margin: 0 3px;
		}

		&:last-child {
			&:after {
				display: none; 
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	List Rating
\* ------------------------------------------------------------ */

.list-rating {
	display: flex; 
	flex-wrap: wrap;
	margin-left: 2px; 

	/*Mobile*/
	@include breakpoint-mobile {
		margin-left: 0; 
	}

	li {
		margin-right: 2px; 

		&:last-child {
			margin-right: 0; 
		}
	}

	&--secondary {
		li {
			margin-right: 4px; 
		}
	}

	&--tertiary {
		li {
			margin-right: 6px; 
		}
	}
}

/* ------------------------------------------------------------ *\
	List Checkboxes
\* ------------------------------------------------------------ */

.list-checkboxes {
	li {
		margin-bottom: 12px; 

		&:last-child {
			margin-bottom: 0; 
		}
	}
}


/* ------------------------------------------------------------ *\
	List Tales
\* ------------------------------------------------------------ */

.list-tales {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -4px;
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		margin: 0 -2px;
	}

	li {
		position: relative; 
		width: 50%; 
		padding: 0 4px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 2px;
		}

		&:nth-child(n+3) {
			margin-top: 8px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-top: 4px; 
			}
		}
	}

	.list__content {
		@include center;
		width: 100%; 
		padding: 45px 20px 20px;
		color: $c-white;

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}

		h3 {
			margin-bottom: 21px; 
			font-size: 30px; 
			color: $c-white;
			text-transform: uppercase; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		.btn-text {
			text-transform: uppercase; 

			i {
				margin-left: 5px; 
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	List Grid
\* ------------------------------------------------------------ */

.list-grid {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -8px;

	/*Mobile*/
	@include breakpoint-mobile {
		margin: 0 -5px;
	}

	li {
		padding: 0 8px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 5px;
		}

		&.current {
			i {
				opacity: 0; 
				visibility: hidden; 

				+ i {
					opacity: 1; 
					visibility: visible; 
				}
			}
		}
	}

	a {
		position: relative; 
		display: block; 
	}

	i {
		display: block; 
		transition: opacity $t-duration, visibility $t-duration;

		+ i {
			position: absolute; 
			top: 0; 
			left: 0; 
			opacity: 0; 
			visibility: hidden; 
		}
	}
}

/* ------------------------------------------------------------ *\
	List Product Number
\* ------------------------------------------------------------ */

.list-product-number {
	display: flex;
	flex-wrap: wrap;
	border-radius: 10px; 
	background: $c-white;
	font-size: 12px; 
	font-weight: bold; 
	text-align: center; 

	li {
		&.current {
			a {
				background: $c-grey-dark;
				color: $c-white;
			}
		}
	}

	a {
		display: block; 
		min-width: 50px;
		padding: 3px 10px;
		border-radius: 10px; 
		color: $c-blue-dark;
	}
}


/* ------------------------------------------------------------ *\
	List Instagram
\* ------------------------------------------------------------ */

.list-instagram {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -63px;
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		min-width: 700px;
		margin: 0; 
	}

	li {
		width: 33.33%; 
		padding: 0 62px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 5px;
		}
	}
}

/* ------------------------------------------------------------ *\
	List Links
\* ------------------------------------------------------------ */

.list-links {
	li {
		margin-bottom: 10px;

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 18px; 
		}

		&:last-child {
			margin-bottom: 0; 
		} 
	}
}

/* ------------------------------------------------------------ *\
	List Links Secondary
\* ------------------------------------------------------------ */

.list-links-secondary {
	li {
		margin-bottom: 30px;

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 13px; 
		}

		&:last-child {
			margin-bottom: 0; 
		} 
	}

	.btn-text {
		font-size: 12px; 
		color: #ae1232;
		text-transform: uppercase; 

		i {
			margin-left: 5px; 
		}
	}
}

/* ------------------------------------------------------------ *\
	List Links Tertiary
\* ------------------------------------------------------------ */

.list-links-tertiary {
	li {
		margin-bottom: 15px;

		&:last-child {
			margin-bottom: 0; 
		} 
	}
}

/* ------------------------------------------------------------ *\
	List Cards
\* ------------------------------------------------------------ */

.list-cards {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -6px 0 -3px;

	/*Mobile*/
	@include breakpoint-mobile {
		margin: 0 -2px;
	}

	li {
		padding: 0 3px;
		margin-bottom: 4px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 2px;
		}
	}
}


/* ------------------------------------------------------------ *\
	List Search
\* ------------------------------------------------------------ */

.list-search {
	display: flex; 
	flex-wrap: wrap;
	padding-left: 2px; 
	margin-top: 17px; 
	font-size: 12px; 
	font-weight: bold; 

	/*Mobile*/
	@include breakpoint-mobile {
		display: block; 
		margin-top: 20px; 
		margin-left: -3px; 
	}

	&:first-child {
		margin-top: 0; 
	}

	li {
		margin-right: 23px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-right: 0; 
			margin-bottom: 13px; 
		}

		&:last-child {
			margin-right: 0; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 0; 
			}
		}
	}

	span {
		color: $c-grey-dark;
	}
}

/* ------------------------------------------------------------ *\
	List Gallery
\* ------------------------------------------------------------ */

.list-gallery {
	max-width: 220px; 
	padding: 0 8px;
	margin-right: auto; 
	margin-left: auto; 
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 200px;
		padding: 0 8px;
	}

	li {
		padding: 0 3px;
		font-size: 0; 

		&.current {
			a:before {
				opacity: 0; 
				visibility: hidden; 
			}
		}
	}

	a {
		position: relative; 
		display: inline-block; 

		&:before {
			@include full;
			content: ''; 
			background: rgba(0,0,0,.03);
			transition: opacity $t-duration, visibility $t-duration;
		}

		&:hover:before {
			opacity: 0; 
			visibility: hidden; 
		}
	}

	img {
		margin: 0 auto;
	}

	.slick-list {
		overflow: hidden !important; 
	}

	.slick-arrow {
		position: absolute; 
		top: 50%;
		transform: rotate(45deg) translateY(-50%);
		width: 6px; 
		height: 6px; 
		border-width: 0;
		border-style: solid; 
		border-color: $c-blue-dark; 
		background: none;
		font-size: 0; 
		appearance: none;

		/*Mobile*/
		@include breakpoint-mobile {
			width: 8px; 
			height: 8px; 
			margin-top: -1px; 
		}
	}

	.slick-prev {
		left: 0; 
		border-width: 0 0 1px 1px;
	}

	.slick-next {
		right: 4px; 
		border-width: 1px 1px 0 0;
	}
}

/* ------------------------------------------------------------ *\
	List Price
\* ------------------------------------------------------------ */

.list-price {
	display: flex;
	flex-wrap: wrap;
	font-size: 12px; 
	color: $c-grey-dark;

	strong {
		color: #767676;
	}

	li {
		position: relative; 
		padding-right: 16px; 

		&:after {
			content: ''; 
			position: absolute; 
			top: 0px; 
			right: 8px;
			width: 1px; 
			height: 12px; 
			background: $c-grey-dark;
		}

		&:last-child {
			padding-right: 0; 

			&:after {
				display: none; 
			}
		}
	}

	&--secondary {
		li {
			padding-right: 10px; 

			/*Mobile*/
			@include breakpoint-mobile {
				&:first-child {
					order: 2;
					padding-right: 0; 
				}

				&:last-child {
					order: 1;
					padding-right: 13px; 

					&:after {
						display: inline-block; 
					}
				}
			}

			&:after {
				display: none; 

				/*Mobile*/
				@include breakpoint-mobile {
					right: 6px;
				}
			}
		}

		strong {
			color: $c-blue-dark;

			/*Mobile*/
			@include breakpoint-mobile {
				color: #787878;
			}
		}
	}

	&--tertiary {
		font-size: 11px; 

		li {
			padding-right: 11px; 

			&:after {
				top: 1px; 
				right: 4px; 
			}
		}

		strong {
			color: $c-blue-dark;
		}
	}
}

/* ------------------------------------------------------------ *\
	List Count
\* ------------------------------------------------------------ */

.list-count { 
	display: flex; 

	a {
		text-decoration: none; 
	}

	li {
		margin: 0 2px;

		i {
			display: inline-block; 
			vertical-align: middle; 
		}
	}

	input {
		width: 34px; 
		border: 0;
		border-radius: 0; 
		background: none;
		font-size: 14px; 
		line-height: 1.2; 
		text-align: center; 
		appearance: none;

		&[type=number]::-webkit-inner-spin-button, 
		&[type=number]::-webkit-outer-spin-button { 
		  -webkit-appearance: none; 
		  margin: 0; 
		}
	}
}
