/* ------------------------------------------------------------ *\
	Rating
\* ------------------------------------------------------------ */

.rating {
	display: flex; 
	flex-wrap: wrap;

	.rating__aside {
		margin-right: 18px; 

		strong {
			font-size: 30px; 
		}
	}

	.rating__content {
		flex: 1;
		margin-top: 8px; 
		text-align: center; 

		.list-rating {
			margin-top: 3px; 
		}
	}

	.rating__bar {
		max-width: 206px;
		width: 100%; 
		height: 8px; 
		border-radius: 10px; 
		margin-left: 2px; 
		background: $c-white;

		span {
			display: block; 
			width: 0%; 
			height: 8px; 
			border-radius: 10px; 
			margin-bottom: 5px; 
			background: $c-grey-dark;
			transition: width $t-duration;
		}
	}

	&--grey {
		margin-left: 3px; 
		color: $c-grey-dark;

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 20px; 
			margin-left: 0; 
		}
	}
}
