/* ------------------------------------------------------------ *\
	Helpers
\* ------------------------------------------------------------ */

/*  Clear  */

.clear {
	@util clearfix;
}

/*  Notext  */

.notext {
	@util text-hide;
}

/*  Hidden  */

[hidden],
.hidden {
	display: none !important;
}

/*  Alignleft  */

.alignleft {
	float: left;
}

/*  Mobile Clone  */
.mobile-clone {
	display: none !important; 
}

/*  Alignright  */

.alignright {
	float: right;
}

/*  Disabled  */

[disabled],
.disabled {
	cursor: default;
}

/*  Grid  */

.col {
	max-width: 100%;
	flex: 1;
	padding: 0 15px;

	&s {
		display: flex;
		flex-flow: row wrap;
		margin: 0 -15px;

		&--v-center {
			align-items: center;
		}

		&--h-center {
			justify-content: center;
		}

		&--h-right {
			justify-content: flex-end;

			/*Mobile*/
			@include breakpoint-mobile {
				justify-content: flex-start;
			}
		}

		&--space-between {
			justify-content: space-between;
		}
	}

	&--1of2 {
		max-width: 50%;
		flex: 0 0 50%;
	}

	&--1of3 {
		max-width: 33.33%;
		flex: 0 0 33.33%;
	}

	&--2of3 {
		max-width: 66.67%;
		flex: 0 0 66.67%;
	}

	&--1of4 {
		max-width: 25%;
		flex: 0 0 25%;
	}

	&--1of5 {
		max-width: 20%;
		flex: 0 0 20%;
	}

	&--2of5 {
		max-width: 40%;
		flex: 0 0 40%;
	}

	&--3of5 {
		max-width: 60%;
		flex: 0 0 60%;
	}

	&--4of5 {
		max-width: 80%;
		flex: 0 0 80%;
	}

	&--size1 {
		max-width: 61%;
		flex: 0 0 61%;
	}

	&--size2 {
		max-width: 29%;
		flex: 0 0 29%;
	}

	&--size3 {
		max-width: 58%;
		flex: 0 0 58%;
	}

	&--size4 {
		max-width: 22.5%;
		flex: 0 0 22.5%;
	}

	&--size5 {
		max-width: 70%;
		flex: 0 0 70%;
	}

	&--size6 {
		max-width: 51.5%;
		flex: 0 0 51.5%;
	}

	&--size7 {
		max-width: 31.5%;
		flex: 0 0 31.5%;
	}

	&--size8 {
		max-width: 31.5%;
		flex: 0 0 31.5%;
	}

	&--size9 {
		max-width: 31.5%;
		flex: 0 0 31.5%;
	}

	&--size10 {
		max-width: 38.7%;
		flex: 0 0 38.7%;
	}

	&--size11 {
		max-width: 33%;
		flex: 0 0 33%;
	}

	&--size12 {
		max-width: 28.3%;
		flex: 0 0 28.3%;
	}

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 100%;
		flex: 0 0 100%;
	}
}

/*  Responsive Helpers  */

.visible-xs-block,
.visible-xs-inline,
.visible-xs-inline-block,
.visible-sm-block,
.visible-sm-inline,
.visible-sm-inline-block,
.visible-md-block,
.visible-md-inline,
.visible-md-inline-block,
.visible-lg-block,
.visible-lg-inline,
.visible-lg-inline-block {
	display: none !important;
}

@media (max-width: $responsive-xs) {
	.hidden-xs {
		display: none !important;
	}
	.visible-xs-block {
		display: block !important;
	}
	.visible-xs-inline {
		display: inline !important;
	}
	.visible-xs-inline-block {
		display: inline-block !important;
	}
}

@media (min-width: $responsive-xs + 1) and (max-width: $responsive-sm) {
	.hidden-sm {
		display: none !important;
	}
	.visible-sm-block {
		display: block !important;
	}
	.visible-sm-inline {
		display: inline !important;
	}
	.visible-sm-inline-block {
		display: inline-block !important;
	}
}

@media (min-width: $responsive-sm + 1) and (max-width: $responsive-md) {
	.hidden-md {
		display: none !important;
	}
	.visible-md-block {
		display: block !important;
	}
	.visible-md-inline {
		display: inline !important;
	}
	.visible-md-inline-block {
		display: inline-block !important;
	}
}

@media (min-width: $responsive-md + 1) {
	.hidden-lg {
		display: none !important;
	}
	.visible-lg-block {
		display: block !important;
	}
	.visible-lg-inline {
		display: inline !important;
	}
	.visible-lg-inline-block {
		display: inline-block !important;
	}
}
