/* ------------------------------------------------------------ *\
	Form Elements
\* ------------------------------------------------------------ */

input::placeholder {
	color: inherit;
	opacity: 1;
}

textarea::placeholder {
	color: inherit;
	opacity: 1;
}

input:-webkit-autofill {
	-webkit-text-fill-color: inherit;
	-webkit-box-shadow: 0 0 0 1000px #fff inset;
}

/* ------------------------------------------------------------ *\
	Field
\* ------------------------------------------------------------ */

.field {
	width: 100%; 
	height: 46px; 
	padding: 0;
	border: 0;
	border-bottom: 1px solid $c-blue-dark; 
	border-radius: 0;
	background: $c-white;
	font-size: 13px; 
	line-height: 45px; 
	color: $c-blue-dark;
	font-weight: normal; 
	transition: border $t-duration, background $t-duration;
	appearance: none;

	&--sm {
		height: auto; 
		padding-bottom: 7px; 
		font-size: 12px; 
		line-height: 1.2; 
		font-weight: bold; 
	}

	&--transparent {
		background: transparent;
	}
}

/* ------------------------------------------------------------ *\
	Textarea
\* ------------------------------------------------------------ */

.textarea {
	display: block; 
	width: 100%; 
	height: 180px; 
	padding: 10px 0; 
	border: 0; 
	border-bottom: 1px solid $c-black;
	border-radius: 0;
	background: $c-white; 
	font-size: 13px; 
	line-height: 1.15; 
	color: $c-black;
	transition: border $t-duration, background $t-duration;
	appearance: none;
	resize: none;
}

/* ------------------------------------------------------------ *\
	Selectric
\* ------------------------------------------------------------ */

.selectric {
	position: relative; 
	min-width: 182px;
	border: none;
	background: none;
	text-align: left; 

	/*Mobile*/
	@include breakpoint-mobile {
		min-width: 114px;
	}

	&:before {
		content: ''; 
		position: absolute; 
		right: 9px; 
		bottom: 2px; 
		left: 9px; 
		height: 1px; 
		background: $c-grey-dark;

		/*Mobile*/
		@include breakpoint-mobile {
			bottom: 0; 
			background: #cd0130;
		}
	}

	.label {
		height: 29px; 
		margin: 0 28px 0 9px;
		font-size: 10px; 
		line-height: 29px; 
		color: $c-blue-dark;
		font-weight: bold; 
		text-transform: uppercase; 

		/*Mobile*/
		@include breakpoint-mobile {
			height: auto; 
			padding: 8px 0;
			text-transform: none; 
			font-size: 12px; 
			line-height: 1.2; 
		}
	}

	.button {
		position: absolute; 
		top: 7px; 
		right: 10px; 
		transform: rotate(45deg);
		width: 7px; 
		height: 7px; 
		border-width: 0 1px 1px 0;
		border-color: $c-grey-dark; 
		border-style: solid; 
		background: none;

		/*Mobile*/
		@include breakpoint-mobile {
			width: 10px; 
			height: 10px; 
			border-width: 0 2px 2px 0; 
			border-color: $c-blue-dark; 
		}

		&:after {
			display: none; 
		}
	}

	&-open {
		background: #dbdbdb;

		.selectric {
			&:before {
				background: $c-grey-dark;
			}
		}
	}

	&-above {
		.selectric-items {
			top: 100%;
			bottom: auto;
		}
	}

	&-items {
		border: 0;
		background: #dbdbdb;
		text-align: left; 
		box-shadow: none;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 8px;
		}

		li {
			font-size: 10px; 
			line-height: 1.2; 
			color: $c-blue-dark;
			text-transform: uppercase; 

			/*Mobile*/
			@include breakpoint-mobile {
				padding: 13px 0 12px;
				border-bottom: 1px solid $c-grey-dark; 
				font-size: 12px; 
				text-transform: none; 

				&:last-child {
					border-bottom-color: transparent;
				}
			}

			&.disabled.selected {
				display: none; 
			}
		}
	}
}

.select--primary {
	.selectric {
		background: $c-grey;

		/*Mobile*/
		@include breakpoint-mobile {
			min-width: 50px;
		}

		&:before {
			display: none; 
		}

		.label {
			height: 33px; 
			margin: 0 40px 0 20px;
			font-size: 12px; 
			line-height: 33px; 
			text-transform: uppercase; 

			/*Mobile*/
			@include breakpoint-mobile {
				padding: 0;
				margin: 0 13px 0 4px;
				font-size: 10px; 
			}
		}

		.button {
			top: 12px; 
			right: 22px; 

			/*Mobile*/
			@include breakpoint-mobile {
				top: 12px; 
				right: 6px; 
				width: 6px; 
				height: 6px; 
				border-width: 0 1px 1px 0; 
			}
		}

		&-items {
			/*Mobile*/
			@include breakpoint-mobile {
				padding: 0;
			}

			li {
				padding-left: 20px; 
				font-size: 12px; 
				text-transform: uppercase; 

				/*Mobile*/
				@include breakpoint-mobile {
					padding: 8px 4px;
					font-size: 10px; 
				}
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Checkbox
\* ------------------------------------------------------------ */

.checkbox {
	position: relative; 
	padding-top: 2px; 

	label {
		position: relative; 
		display: inline-block; 
		padding-left: 20px; 
		font-size: 12px; 
		color: $c-grey-dark;
		font-weight: normal; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding-left: 18px; 
		}

		&:before {
			content: ''; 
			position: absolute; 
			top: 2px; 
			left: 0; 
			width: 11px; 
			height: 11px; 
			border: 1px solid $c-grey-dark;
			background: $c-white;
		}

		&:after {
			content: ''; 
			position: absolute; 
			top: 4px; 
			left: 2px; 
			width: 7px; 
			height: 7px; 
			background: transparent;
		}
	}

	input {
		display: none; 

		&:checked + label {
			/*Mobile*/
			@include breakpoint-mobile {
				font-weight: bold; 
			}

			&:after {
				background: $c-grey-dark;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Radio Circle
\* ------------------------------------------------------------ */

.radio-circle {
	label {
		display: block; 
		width: 30px; 
		height: 30px; 
		background: $c-grey;
		border-radius: 100%; 
		font-size: 12px; 
		line-height: 30px; 
		color: $c-grey-dark;
		text-transform: uppercase; 
		text-align: center; 
	}

	input {
		display: none; 

		&:checked + label {
			font-weight: bold; 
			color: $c-blue-dark;
		}
	}
}

