/* ------------------------------------------------------------ *\
	Header
\* ------------------------------------------------------------ */

.header {
	position: relative; 
	z-index: 100; 

	/*Mobile*/
	@include breakpoint-mobile {
		+ .section-image {
			padding-top: 0; 

			.col--size1 {
				max-width: 100%;
				flex: 0 0 100%;

				img {
					margin: 0 auto;
				}
			}

			&--swap {
				.section__entry {
					margin: 0 auto 21px;
				}
			}
		}
	}
	
	.shell {
		max-width: (1220px + $shell-gutter * 2);
	}

	.header__bar { 
		padding: 11px 0;
		background: $c-blue-dark;
		font-size: 9px; 
		color: $c-grey-light; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}

		> .shell {
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
		}

		p {
			margin-bottom: 0; 

			strong {
				font-size: 11px; 
			}
		}
	}

	.header__content {
		position: relative; 
		padding: 28px 0 22px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 16px 0 17px;
		}

		> .shell {
			display: flex; 
			justify-content: space-between; 
			align-items: center;
			flex-wrap: wrap;
		}

		&-inner {
			display: flex;
			align-items: center;
			flex-wrap: wrap;

			/*Mobile*/
			@include breakpoint-mobile {
				justify-content: space-between;
				width: 100%; 
			}

			[class^="btn-"] {
				&:hover {
					opacity: .8; 
				}
			}

			.btn-profile {
				margin-right: 20px; 

				&:last-child {
					margin-right: 0; 
				}
			}

			.btn-search {
				margin-right: 35px; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-right: 0; 
				}
			}
		}
	}

	.logo {
		/*Mobile*/
		@include breakpoint-mobile {
			position: absolute; 
			top: 10px; 
			left: 50%;
			transform: translateX(-50%); 
		}
	}
}
