/* ------------------------------------------------------------ *\
	Nav
\* ------------------------------------------------------------ */

.nav-wrapper {
	margin-top: -1px; 
	margin-left: 60px; 

	/*Mobile*/
	@include breakpoint-mobile {
		position: absolute; 
		top: 100%;
		right: 0; 
		left: 0; 
		display: none; 
		margin: 0;
		background: $c-grey-light;
	}

	.list-nav {
		display: none; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: flex; 
		}
	}

	.nicescroll-rails {
		z-index: 55 !important;
		transform: translateX(-6px);
	}
}

.nav {
	font-size: 12px; 
	font-weight: bold; 

	/*Mobile*/
	@include breakpoint-mobile {
		margin-bottom: 51px; 

		&:last-child {
			margin-bottom: 0; 
		}
	}

	a {
		text-decoration: none; 

		> img {
			transition: opacity $t-duration;
		}

		&:hover {
			> img {
				opacity: .8; 
			}
		}
	}

	> ul {
		display: flex; 
		flex-wrap: wrap;
		margin: 0 -22px;

		/*Mobile*/
		@include breakpoint-mobile {
			display: block; 
			margin: 0;
		}

		> li {
			margin: 0 22px;

			/*Mobile*/
			@include breakpoint-mobile {
				position: relative; 
				display: flex; 
				justify-content: space-between;
				align-items: center;
				flex-wrap: wrap;
				margin: 0;

				&:after {
					content: ''; 
					position: absolute; 
					right: 15px; 
					bottom: -1px; 
					left: 15px; 
					height: 1px; 
					background: #bdbdbd;
				}
			}

			> i {
				display: none; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-top: 3px; 
					margin-right: 16px; 
					display: inline-block; 
					transition: top $t-duration, transform $t-duration;
				}
			}

			> a {
				position: relative; 
				display: inline-block; 
				padding: 7px 0;
				margin: 0 15px;
				color: $c-blue-dark;

				/*Mobile*/
				@include breakpoint-mobile {
					position: static; 
					display: block; 
					padding: 22px 5px 23px;
					font-size: 16px; 
					font-weight: bold; 
				}

				&:after {
					content: ''; 
					position: absolute; 
					right: 0; 
					bottom: -12px; 
					left: 0; 
					height: 2px; 
					background: transparent;
					transition: background $t-duration;

					/*Mobile*/
					@include breakpoint-mobile {
						display: none; 
					}
				}
			}

			&:hover {
				> a:after {
					background: #ae1232;

					/*Mobile*/
					@include breakpoint-mobile {
						background: #bdbdbd;
					}
				}

				.nav__dropdown {
					opacity: 1; 
					visibility: visible; 
				}
			}

			&.hover {
				> i {
					transform: rotate(180deg);
				}
			}
		}
	}

	.nav__dropdown {
		position: absolute; 
		top: 100%;
		left: 50%;
		z-index: 50;
		transform: translateX(-50%); 
		width: 100%;  
		background: $c-grey-light;
		box-shadow: 0 20px 20px 4px rgba(0,0,0,.14);
		opacity: 0; 
		visibility: hidden; 
		transition: opacity $t-duration, visibility $t-duration;

		/*Mobile*/
		@include breakpoint-mobile {
			position: relative; 
			top: auto;
			left: auto;
			transform: none;
			display: none; 
			width: 100%; 
			opacity: 1; 
			visibility: visible; 
			box-shadow: none;
		}

		&:before {
			content: ''; 
			position: absolute; 
			top: -25px; 
			width: 100%; 
			height: 25px; 
			background: transparent;

			/*Mobile*/
			@include breakpoint-mobile {
				top: 0; 
				right: 15px; 
				left: 15px; 
				width: auto; 
				height: 1px; 
				background: #bdbdbd;
			}
		}

		&:after {
			content: ''; 
			position: absolute; 
			top: 13px; 
			right: 0; 
			left: 0; 
			max-width: 1220px;
			height: 1px; 
			margin: 0 auto;
			background: #eaeaea;

			/*Mobile*/
			@include breakpoint-mobile {
				display: none; 
			}
		}

		> ul {
			display: flex;
			flex-wrap: wrap;
			max-width: 1250px;
			padding: 65px 15px 63px 98px;
			margin: 0 auto;

			/*Mobile*/
			@include breakpoint-mobile {
				position: relative; 
				display: block; 
				padding: 17px 10px 15px;
				margin: 0 15px;
				font-weight: normal; 
				transition: padding $t-duration, margin $t-duration;
			}

			> li {
				width: 16.3%; 
				padding: 0 15px;

				/*Mobile*/
				@include breakpoint-mobile {
					display: flex; 
					justify-content: space-between;
					flex-wrap: wrap;
					width: 100%; 
					padding: 15px 5px 14px;
					border-bottom: 1px solid #bdbdbd; 
					transition: width $t-duration, padding $t-duration, border $t-duration, background $t-duration;

					&:last-child {
						border-bottom: 0; 
					}
				}

				> i {
					display: none; 

					/*Mobile*/
					@include breakpoint-mobile {
						display: inline-block; 
						transform: rotate(-90deg);
						margin-top: 2px; 
						margin-right: 1px; 
						transition: transform $t-duration;
					}
				}

				> a {
					font-size: 13px; 

					/*Mobile*/
					@include breakpoint-mobile {
						font-size: 12px; 
					}
				}

				> ul {
					margin-top: 25px;

					/*Mobile*/
					@include breakpoint-mobile {
						position: absolute; 
						top: 1px; 
						bottom: 0; 
						right: 15px; 
						left: 50%; 
						transform: translateX(150%); 
						z-index: 5;
						display: block; 
						padding: 26px 19px 10px 13px;
						margin-top: 0; 
						background: $c-white;
						transition: transform $t-duration;
					}

					li {
						margin-bottom: 12px; 

						/*Mobile*/
						@include breakpoint-mobile {
							padding: 15px 6px 14px;
							border-bottom: 1px solid #c6c6c6; 
							margin-bottom: 0; 
							color: #9c9c9c;
							font-weight: bold; 
						}

						&:last-child {
							margin-bottom: 0; 

							/*Mobile*/
							@include breakpoint-mobile {
								border-bottom: 0; 
							}
						}

						a {
							&:hover {
								text-decoration: underline; 
							}
						}
					}
				}

				&.nav__image {
					width: 18.5%; 

					/*Mobile*/
					@include breakpoint-mobile {
						display: none; 
					}
				}

				&.nav__all {
					display: none; 

					/*Mobile*/
					@include breakpoint-mobile {
						display: flex; 
					}

					> a {
						font-weight: bold; 
					}
				}

				&.hover {
					> a {
						color: $c-grey-dark;
						font-weight: bold; 
					}
				}
			}
		}

		.btn-text {
			color: #ae1232;

			i {
				margin-left: 4px; 
			}
		}

		&.expanded {
			> ul {
				padding: 25px 0 7px;
				margin: 0;

				> li {
					width: 50%; 
					padding: 14px 6px 15px 15px;
					border-bottom-color: transparent; 

					&.hover {
						margin-right: -5px; 
						background: $c-white;

						i {
							transform: rotate(90deg) translateY(5px); 
						}

						ul {
							transform: translateX(0); 
						}
					}
				}
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Nav Utilities
\* ------------------------------------------------------------ */

.nav-utilities {
	font-size: 9px; 
	text-transform: uppercase; 

	ul {
		display: flex; 
		flex-wrap: wrap;
		margin: 0 -12px;
	}

	li {
		padding: 0 12px;
	}

	a {
		color: $c-grey-light;
	}
}
