/* ------------------------------------------------------------ *\
	Fonts
\* ------------------------------------------------------------ */

@font-face {
    font-family: 'Proxima Nova';
    src: url('../fonts/ProximaNova-Regular.woff2') format('woff2'),
        url('../fonts/ProximaNova-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima Nova';
    src: url('../fonts/ProximaNova-Bold.woff2') format('woff2'),
        url('../fonts/ProximaNova-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'F37Bolton';
    src: url('../fonts/F37Bolton-Regular.woff2') format('woff2'),
        url('../fonts/F37Bolton-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'F37Bolton';
    src: url('../fonts/F37Bolton-Italic.woff2') format('woff2'),
        url('../fonts/F37Bolton-Italic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'F37Bolton';
    src: url('../fonts/F37Bolton-Bold.woff2') format('woff2'),
        url('../fonts/F37Bolton-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'F37Bolton';
    src: url('../fonts/F37Bolton-BoldItalic.woff2') format('woff2'),
        url('../fonts/F37Bolton-BoldItalic.woff') format('woff');
    font-weight: bold;
    font-style: italic;
}

