/* ------------------------------------------------------------ *\
	Slider
\* ------------------------------------------------------------ */

.slider {
	font-size: 14px; 
	line-height: 1.25; 
	color: $c-blue-dark;

	/*Mobile*/
	@include breakpoint-mobile {
		color: $c-white;
		text-align: center; 
	}

	.slide {
		.cols {
			align-items: center;

			/*Mobile*/
			@include breakpoint-mobile {
				position: relative; 
				display: block; 
				max-width: 400px;
				margin: 0 auto;
				
				img {
					width: 100%; 
				}
			}
		}

		.col {
			/*Mobile*/
			@include breakpoint-mobile {
				padding: 0;
			}

			&--1of2 {
				&:first-child {
					padding-right: 50px; 

					/*Mobile*/
					@include breakpoint-mobile {
						position: absolute; 
						right: 0; 
						bottom: 0; 
						left: 0; 
						padding: 37px 10px;
						z-index: 5;
					}
				}

				&:last-child {
					padding-right: 12px; 
					padding-left: 0; 

					/*Mobile*/
					@include breakpoint-mobile {
						padding-right: 0; 
					}
				}
			}
		}

		h1 {
			max-width: 310px;
			margin-bottom: 26px; 
			font-size: 40px; 
			line-height: 1.2; 

			/*Mobile*/
			@include breakpoint-mobile {
				max-width: 100%;
				margin-bottom: 9px; 
				color: $c-white;
			}

			&:last-child {
				margin-bottom: 0; 
			}
		}

		p {
			max-width: 310px;
			margin-bottom: 34px; 

			/*Mobile*/
			@include breakpoint-mobile {
				display: none; 
			}

			&:last-child {
				margin-bottom: 0; 
			}
		}

		.btn {
			min-width: 143px;
			padding: 0 17px;

			/*Mobile*/
			@include breakpoint-mobile {
				min-width: auto;
				height: auto; 
				padding: 0;
				border: 0;
				border-radius: 0; 
				background: none;
				line-height: 1.2; 
			}

			i {
				margin-left: 1px; 
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Slider Mobile
\* ------------------------------------------------------------ */

.slider-mobile {
	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 360px;
		display: block; 
		margin: 0 auto;

		.slick-list {
			overflow: visible; 
		}

		.slick-slide {
			padding: 0 15px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Slider Mobile Secondary
\* ------------------------------------------------------------ */

.slider-mobile-secondary {
	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 245px;
		display: block; 
		margin: 0 auto;

		.slick-list {
			overflow: visible; 
		}

		.slick-slide {
			padding: 0 20px;
		}

		.slick-dots {
			margin-top: 1px; 
		}
	}
}

/* ------------------------------------------------------------ *\
	Slider Featured
\* ------------------------------------------------------------ */

.slider-featured {
	padding: 0 20px;

	/*Mobile*/
	@include breakpoint-mobile {
		max-width: 245px;
		padding: 0; 
		margin: 0 auto;
	}

	.slick-list {
		overflow: visible; 
	}

	.slick-dots {
		margin-top: 2px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-top: 7px; 
		}
	}

	.slide {
		padding: 0 82px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 20px;
		}

		.col--1of3 {
			display: flex;
			flex-wrap: wrap;

			.product {
				margin: 0 auto;
			}

			&:first-child {
				.product {
					margin-left: 0;
				}
			}

			&:last-child {
				.product {
					margin-right: 0; 
				}
			}
		}
	}

	.product {
		max-width: 205px;
		flex: 0 0 205px;

		/*Mobile*/
		@include breakpoint-mobile {
			.list-gallery {
				.slick-arrow {
					width: 6px; 
					height: 6px; 
				}
			}
		}

		.product__image:before {
			@include full;
			content: ''; 
			background: #b12f33;
			mix-blend-mode: multiply;
			transition: opacity $t-duration, visibility $t-duration, background $t-duration;
		}

		.product__content {
			opacity: 0; 
			visibility: hidden; 
			transition: opacity $t-duration, visibility $t-duration;

			/*Mobile*/
			@include breakpoint-mobile {
				h3 {
					margin-bottom: 2px; 
				}

				.btn-text {
					height: auto; 
					padding: 0; 
					border: 0;
					border-radius: 0; 
					background: none;
					font-size: 10px; 
					line-height: 1.2; 
					color: $c-red;
				}
			}
		}

		.product__wishlist {
			/*Mobile*/
			@include breakpoint-mobile {
				top: 8px; 
				left: 10px; 

				.ico-heart {
					width: 14px; 
					height: 11px; 
				}
			}
		}
	}

	.slick-active {
		.product {
			.product__image:before {
				opacity: 0; 
				visibility: hidden; 
			}

			.product__content {
				opacity: 1; 
				visibility: visible; 
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Slider Product
\* ------------------------------------------------------------ */

.slider-product {
	.slide {
		position: relative; 

		/*Mobile*/
		@include breakpoint-mobile {
			img {
				width: 100%; 
			}
		}
	}

	.slick-dots {
		margin-top: 20px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-top: 9px; 
		}
	}
}


