/* ------------------------------------------------------------ *\
	Socials
\* ------------------------------------------------------------ */

.socials {
	ul {
		list-style: none; 
		display: flex; 
		align-items: center;
		flex-wrap: wrap;
		margin: 0 -9px;

		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 -11px;
		}

		li {
			padding: 0 9px;

			/*Mobile*/
			@include breakpoint-mobile {
				padding: 0 11px;
			}

			i {
				display: block; 
			}
		}
	}
}
