/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

body {
	min-width: 320px;
	padding-bottom: 0; 
	background: url(../images/lines-grid.png) repeat center $c-grey-light;
	font-family: $f-f37bolton;
	font-size: $font-size-base;
	line-height: $line-height-base;
	color: $c-blue-dark;
	font-weight: normal; 

	/*Mobile*/
	@include breakpoint-mobile {
		background-image: none; 
	}
}

a {
	text-decoration: none;
	color: inherit;
	transition: all $t-duration;

	&:hover {
		text-decoration: underline;
	}

	&[href^="tel"] {
		&:hover,
		&:focus {
			text-decoration: none;
		}
	}
}

p,
ul,
ol,
dl,
hr,
table,
blockquote { 
	margin-bottom: 1.22em; 
}

h1[class],
h2[class],
h3[class],
h4[class],
h5[class],
h6[class],
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
p:last-child,
ul:last-child,
ol:last-child,
dl:last-child,
table:last-child,
blockquote:last-child { 
	margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin-top: 0; 
	margin-bottom: 0.62em; 
	line-height: 1.2; 
	color: $c-blue-dark;
	font-weight: bold; 
}

h1 {
	font-size: 48px; 
}

h2 {
	font-size: 40px; 
	text-transform: uppercase; 
}

h3 {
	font-size: 20px; 
}

h4 {
	font-size: 12px; 
}

h5 {
	font-size: 12px; 
	text-transform: uppercase; 
}

h6 {
	font-size: 10px; 
	text-transform: uppercase; 
}


html.browser-ie,
html.browser-edge {
	.section-background:before {
		background: #041753;
		opacity: .8; 
	}

	.slider-featured .product .product__image:before {
		background: #920000;
		opacity: .8; 
	}
}
