/* ------------------------------------------------------------ *\
	Breadcrumbs
\* ------------------------------------------------------------ */

.breadcrumbs {
	ul {
		display: flex; 
		flex-wrap: wrap;
		font-size: 12px; 
		list-style: none; 
		color: $c-grey-dark;

		/*Mobile*/
		@include breakpoint-mobile {
			justify-content: center;
		}
	}

	li {
		position: relative; 
		margin-right: 10px; 

		&:after {
			content: '.'; 
			position: absolute; 
			right: -7px; 
			top: -3px; 

			/*Mobile*/
			@include breakpoint-mobile {
				display: none; 
			}
		}

		&:last-child {
			margin-right: 0; 

			&:after {
				display: none; 
			}
		}
	}

	a {
		color: $c-grey-dark;
	}
}
