/* ------------------------------------------------------------ *\
	Search
\* ------------------------------------------------------------ */

.search-wrapper {
	position: absolute; 
	top: 100%;
	left: 50%;
	transform: translateX(-50%);
	width: 100vw; 
	padding: 18px 0 56px; 
	background: url(../images/lines-grid.png) repeat center $c-grey-light;
	box-shadow: 0 20px 20px 4px rgba(0,0,0,.08);
	opacity: 0; 
	visibility: hidden; 
	transition: opacity $t-duration, visibility $t-duration;

	/*Mobile*/
	@include breakpoint-mobile {
		top: 0; 
		right: 0; 
		left: 0; 
		z-index: 150;
		width: auto; 
		padding: 35px 15px 29px;
		background: $c-grey-light;
		transform: none;  
		box-shadow: none;
	}

	.shell {
		max-width: (900px + $shell-gutter * 2);

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0;
		}
	}

	&.active {
		opacity: 1; 
		visibility: visible; 
	}

	.list-search {
		/*Mobile*/
		@include breakpoint-mobile {
		}
	}
}

.search {
	position: relative; 
	padding-right: 56px; 

	/*Mobile*/
	@include breakpoint-mobile {
		padding-right: 30px; 
		border-bottom: 1px solid $c-grey-dark; 
	}

	form {
		position: relative; 
	}

	.search__field {
		width: 100%;
		padding: 0 50px 22px 0;
		border-width: 0 0 2px;
		border-style: solid; 
		border-color: $c-grey-dark; 
		border-radius: 0; 
		background: none;
		font-size: 40px; 
		line-height: 1.1; 
		color: $c-blue-dark;
		appearance: none;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 80px 17px 0;
			border-bottom-width: 0; 
			font-size: 26px; 

			&::placeholder {
				opacity: 0; 
				color: transparent;
			}
		}

		&:-webkit-autofill {
			-webkit-text-fill-color: inherit;
			-webkit-box-shadow: 0 0 0 100px $c-grey-light inset;
		}
	}

	.search__btn {
		position: absolute; 
		top: 0; 
		right: 0; 
		width: 50px; 
		height: 66px; 
		border: 0; 
		background: none;
		background-size: 27px 27px;
		font-size: 0; 
		line-height: 0; 
		appearance: none;
		transition: opacity $t-duration;

		/*Mobile*/
		@include breakpoint-mobile {
			top: -3px; 
			right: 14px; 
			width: 25px; 
			height: 25px; 
		}

		i {
			position: absolute; 
			top: 19px;
			left: 50%;
			transform: translateX(-50%); 

			/*Mobile*/
			@include breakpoint-mobile {
				top: 5px; 
			}
		}

		&:hover {
			opacity: .8; 
		}
	}

	.search__close {
		position: absolute; 
		top: 18px; 
		right: 8px; 
		width: 30px; 
		height: 30px; 
		background: url(../images/times@2x.png) center no-repeat;
		background-size: 25px 25px;

		/*Mobile*/
		@include breakpoint-mobile {
			top: 1px; 
			right: 8px; 
			width: 20px; 
			height: 20px; 
			background-size: 16px 16px;
		}

		&:hover {
			opacity: .8; 
		}
	}
}
