/* ------------------------------------------------------------ *\
    Box Cart
\* ------------------------------------------------------------ */

.box-cart-wrapper {
    position: absolute; 
    top: 100%;
    right: calc(50vw - 645px); 
    width: 536px; 
    padding: 40px 50px 17px 31px;
    margin-top: 11px; 
    background: $c-white;
    opacity: 0; 
    visibility: hidden; 
    transition: opacity $t-duration, visibility $t-duration;

    @media (max-width: 1290px) {
        right: -15px; 
    }

    /*Mobile*/
    @include breakpoint-mobile {
        top: 40px;
        right: 0; 
        width: 100%; 
        padding: 24px 17px 22px 15px;
        margin-top: 0; 

        background: $c-grey-light;
    }

    &:after {
        content: ''; 
        display: none; 
        position: absolute; 
        top: 0; 
        right: calc(-50vw + 618px); 
        bottom: 0; 
        width: calc(50vw - 618px); 
        background: $c-white;
    }

    &.active {
        opacity: 1; 
        visibility: visible; 
    }
}

.box-cart {
    font-size: 12px; 
    line-height: 1.33; 

    .box__head {
        margin-bottom: 34px; 

        /*Mobile*/
        @include breakpoint-mobile {
            margin-bottom: 23px; 
        }

        &:last-child {
            margin-bottom: 0; 
        }

        h3 {
            font-size: 30px; 
            line-height: 1.47; 

            /*Mobile*/
            @include breakpoint-mobile {
                font-size: 26px; 
                line-height: 1.69; 
                font-weight: normal; 
            }
        }
    }

    .box__body {
        /*Mobile*/
        @include breakpoint-mobile {
            max-height: 375px;
            padding-bottom: 15px; 
        }
    }

    .box__foot {
        display: flex; 
        flex-wrap: wrap;
        margin-top: 33px; 
        color: $c-blue-dark;

        /*Mobile*/
        @include breakpoint-mobile {
            @include clearfix;
            display: block; 
            margin-top: 7px; 
        }

        &:first-child {
            margin-top: 0; 
        }

        h4 {
            margin-bottom: 1px; 
        }

        p {
            margin-bottom: 1px; 

            /*Mobile*/
            @include breakpoint-mobile {
                display: none; 
            }

            &:last-child {
                margin-bottom: 0; 
            }

            span {
                color: #767676;
            }

            em {
                font-size: 11px; 
            }
        }

        &-inner {
            width: 175px; 
            padding-right: 15px; 

            /*Mobile*/
            @include breakpoint-mobile {
                float: left; 
                width: auto; 
            }
        }

        .list-price {
            /*Mobile*/
            @include breakpoint-mobile {
                font-size: 14px; 

                strong {
                    color: $c-blue-dark;
                }
            }
        }
    }

    .box__actions {
        width: calc(100% - 175px); 
        margin-top: 3px; 

        /*Mobile*/
        @include breakpoint-mobile {
            float: right; 
            width: auto; 
        }

        .btn {
            width: 100%; 
            font-size: 11px; 

            /*Mobile*/
            @include breakpoint-mobile {
                min-width: 140px;
                padding: 0 12px;
            }
        }
    }
}
