/* ------------------------------------------------------------ *\
	Filter
\* ------------------------------------------------------------ */

.filter {
	position: relative; 
	display: flex; 
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	padding: 5px 12px 8px 11px;
	margin-bottom: 38px; 
	background: #f6f6f6;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 0;
		background: none;

		.select {
			transform: translateY(-7px); 
		}
	}
	
	&:last-child {
		margin-bottom: 0; 
	}

	.filter__content {
		position: absolute; 
		top: 12px; 
		left: 50%;
		transform: translateX(-50%); 
		display: flex; 
		align-items: center;
		flex-wrap: wrap;
		font-size: 10px; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}

		strong {
			text-transform: uppercase; 
		}

		.list-product-number {
			margin-right: 6px; 
			margin-left: 15px;

			&:first-child {
				margin-left: 0; 
			} 
		}
	}
}
