/* ------------------------------------------------------------ *\ 
	# Icons
\* ------------------------------------------------------------ */

[class^="ico-"] { 
	position: relative; 
	display: inline-block;
	vertical-align: top; 
	background-size: 100%;
	font-size: 0; 
	line-height: 0; 
	text-indent: -4004px;
}

.ico {
	&-angle-down {
		text-indent: 0;

		&:before {
			content: ''; 
			transform: rotate(45deg);
			display: inline-block; 
			width: 5px; 
			height: 5px; 
			border-width: 0 1px 1px 0;
			border-color: $c-blue-dark; 
			border-style: solid; 
		}
	}

	&-plus-circle {
		width: 20px; 
		height: 20px; 
		background-image: url(../images/plus-circle@2x.png);
	}

	&-checkbox-checked {
		width: 20px; 
		height: 20px; 
		background-image: url(../images/checkbox-checked@2x.png);
	}

	&-filter {
		width: 20px; 
		height: 21px; 
		background-image: url(../images/filter@2x.png);
	}

	&-zoom {
		width: 17px; 
		height: 17px; 
		background-image: url(../images/zoom@2x.png);
	}

	&-zoom-out {
		width: 20px; 
		height: 21px; 
		background-image: url(../images/zoom-out@2x.png);
	}

	&-grid-full {
		width: 18px; 
		height: 17px; 
		background-image: url(../images/grid-full@2x.png);
	}

	&-grid-full-dark {
		width: 18px; 
		height: 17px; 
		background-image: url(../images/grid-full-dark@2x.png);
	}

	&-grid-xs {
		width: 14px; 
		height: 15px; 
		background-image: url(../images/grid-xs@2x.png);

		/*Mobile*/
		@include breakpoint-mobile {
			width: 16px; 
			height: 17px; 
		}
	}

	&-grid-xs-dark {
