/* ------------------------------------------------------------ *\
	Form
\* ------------------------------------------------------------ */

.form {
	.form__row {
		position: relative; 
	}

	.form__label {
		position: absolute; 
		top: 15px; 
		left: 0; 
		display: inline-block; 
		font-size: 13px; 
		line-height: 1.15; 
		font-weight: normal; 
		color: $c-black;
		transition: top $t-duration;
	}

	&.focused,
	&.filled {
		.form__label {
			top: 0; 
		}
	}
}

/* ------------------------------------------------------------ *\
	Form Calc
\* ------------------------------------------------------------ */

.form-calc {
	position: relative; 
	max-width: 400px;
	margin-bottom: 22px; 

	.form__body {
		width: 51.4%; 
		padding-right: 5px;
	}

	.form__foot {
		margin-top: 20px;
		color: $c-grey-dark; 
	}

	.form__actions {
		position: absolute; 
		top: -3px; 
		right: 0; 
		width: 49%; 
	}

	.form__btn {
		width: 100%; 
		height: auto; 
		font-size: 11px; 
		line-height: .95; 

		i {
			margin-left: 8px; 
			line-height: .95; 
		}
	}
}
