/* ------------------------------------------------------------ *\
    Accordion Filter
\* ------------------------------------------------------------ */

.accordion-filter {
    margin-bottom: 54px; 

    /*Mobile*/
    @include breakpoint-mobile {
        margin-bottom: 28px; 
    }

    &:last-child {
        margin-bottom: 0; 
    }

    .accordion__head {
        position: relative; 
        font-size: 14px; 
        line-height: 1; 
        color: $c-blue-dark;

        /*Mobile*/
        @include breakpoint-mobile {
            font-size: 12px; 
        }

        &:after {
            content: ''; 
            position: absolute; 
            top: 2px; 
            right: 2px; 
            transform: rotate(45deg);
            width: 7px; 
            height: 7px; 
            border-width: 0 1px 1px 0;
            border-color: $c-blue-dark;
            border-style: solid; 
            transition: transform $t-duration, top $t-duration;

            /*Mobile*/
            @include breakpoint-mobile {
                transform: rotate(-45deg);
            }
        }

        a {
            text-decoration: none; 
            
            &:hover {
                text-decoration: none; 
            }
        }
    }

    .accordion__body {
        display: none; 
        padding: 12px 0 16px;

        /*Mobile*/
        @include breakpoint-mobile {
            display: block; 
        }
    }

    .accordion__section {
        margin-bottom: 13px; 

        &:last-child {
            margin-bottom: 0; 
        }
    }

    .list-checkboxes li {
        margin-bottom: 11px; 
    }

    .expanded {
        .accordion__head {
            &:after {
                top: 6px; 
                transform: rotate(225deg);

                /*Mobile*/
                @include breakpoint-mobile {
                    top: 2px; 
                    transform: rotate(-45deg);
                }
            }
        }
    }
}

/* ------------------------------------------------------------ *\
    Accordion Product
\* ------------------------------------------------------------ */

.accordion-product {
    margin: 0 -15px;
    background: $c-grey;

    .list-rating {
        /*Mobile*/
        @include breakpoint-mobile {
            margin: 20px 0 5px;
        }
    }

    .accordion__head {
        position: relative; 
        padding: 0 15px;

        a {
            display: block; 
            padding: 23px 0;
            border-bottom: 1px solid $c-white; 
        }

        h3 {
            font-size: 16px; 
            font-weight: normal; 
        }

        &:after {
            content: ''; 
            position: absolute; 
            top: calc(50% - 3px); 
            right: 17px; 
            transform: translateY(-50%) rotate(45deg); 
            width: 5px; 
            height: 5px; 
            border-width: 0 1px 1px 0;
            border-color: $c-blue-dark;
            border-style: solid; 
            transition: transform $t-duration, top $t-duration;

            /*Mobile*/
            @include breakpoint-mobile {
                transform: rotate(-45deg);
            }
        }

        a {
            text-decoration: none; 
            
            &:hover {
                text-decoration: none; 
            }
        }
    }

    .accordion__body {
        display: none; 
        padding: 27px 15px 0;
        background: $c-grey-light;

        h3 {
            margin-top: 20px; 
            font-size: 15px; 

            &:first-child {
                margin-top: 0; 
            }
        }

        p {
            margin-bottom: 7px; 

            &:last-child {
                margin-bottom: 0; 
            }
        }

        &--text-grey {
            p {
                color: $c-grey-dark;
            }

            strong {
                color: $c-blue-dark;
            }

            .btn-text {
                display: inline-block; 
                margin-top: 10px; 
                font-size: 12px; 
                color: $c-grey-dark;
            }
        }
    }

    .accordion__entry {
        padding-bottom: 25px; 
    }

    .accordion__section {
        &:last-child {
            .accordion__head {
                a {
                    border-bottom-color: transparent; 
                }
            }
        }
    }

    .expanded {
        .accordion__head {
            a {
                border-bottom-color: transparent; 
            }

            &:after {
                transform: translateY(-50%) rotate(45deg);
            }
        }
    }
}

