/* ------------------------------------------------------------ *\
	Variables
\* ------------------------------------------------------------ */

$f-proxima-nova: 'Proxima Nova', sans-serif;
$f-f37bolton: 'F37Bolton', sans-serif;

$font-size-base: 12px;
$line-height-base: 1.2;

$c-black: #000;
$c-white: #fff;
$c-blue-dark: #152342;
$c-red: #b11730;
$c-grey-dark: #999;
$c-grey: #e6e6e6;
$c-grey-light: #f2f2f2;

$shell-max-width: 1020px;

$shell-gutter: 15px;

$t-duration: .4s;

$responsive-xxs: 374px;
$responsive-xs: 1024px;
$responsive-xs3: 600px;
$responsive-xs4: 782px;
$responsive-sm: 1024px;
$responsive-md: 1200px;

$breakpoint-mobile2: '(max-width: #{$responsive-xs3})';
$breakpoint-mobile3: '(max-width: #{$responsive-xs4})';
$breakpoint-mobile-portrait: '(max-width: #{$responsive-xxs}) ';
$breakpoint-mobile: '(max-width: #{$responsive-xs}) ';
$breakpoint-tablet-portrait: '(max-width: #{$responsive-sm}) ';
$breakpoint-small-desktop: '(max-width: #{$responsive-md}) ';
$retina: '(min-resolution: 2dppx) ';
