/* ------------------------------------------------------------ *\
    Accordion Filter
\* ------------------------------------------------------------ */
.accordion-filter {
  margin-bottom: 54px;
  /*Mobile*/ }
  @media (max-width: 1024px) {
    .accordion-filter {
      margin-bottom: 28px; } }
  .accordion-filter:last-child {
    margin-bottom: 0; }
  .accordion-filter .accordion__head {
    position: relative;
    font-size: 14px;
    line-height: 1;
    color: #152342;
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .accordion-filter .accordion__head {
        font-size: 12px; } }
    .accordion-filter .accordion__head:after {
      content: '';
      position: absolute;
      top: 2px;
      right: 2px;
      transform: rotate(45deg);
      width: 7px;
      height: 7px;
      border-width: 0 1px 1px 0;
      border-color: #152342;
      border-style: solid;
      transition: transform 0.4s, top 0.4s;
      /*Mobile*/ }
      @media (max-width: 1024px) {
        .accordion-filter .accordion__head:after {
          transform: rotate(-45deg); } }
    .accordion-filter .accordion__head a {
      text-decoration: none; }
      .accordion-filter .accordion__head a:hover {
        text-decoration: none; }
  .accordion-filter .accordion__body {
    display: none;
    padding: 12px 0 16px;
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .accordion-filter .accordion__body {
        display: block; } }
  .accordion-filter .accordion__section {
    margin-bottom: 13px; }
    .accordion-filter .accordion__section:last-child {
      margin-bottom: 0; }
  .accordion-filter .list-checkboxes li {
    margin-bottom: 11px; }
  .accordion-filter .expanded .accordion__head:after {
    top: 6px;
    transform: rotate(225deg);
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .accordion-filter .expanded .accordion__head:after {
        top: 2px;
        transform: rotate(-45deg); } }

/* ------------------------------------------------------------ *\
    Accordion Product
\* ------------------------------------------------------------ */
.accordion-product {
  margin: 0 -15px;
  background: #e6e6e6; }
  .accordion-product .list-rating {
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .accordion-product .list-rating {
        margin: 20px 0 5px; } }
  .accordion-product .accordion__head {
    position: relative;
    padding: 0 15px; }
    .accordion-product .accordion__head a {
      display: block;
      padding: 23px 0;
      border-bottom: 1px solid #fff; }
    .accordion-product .accordion__head h3 {
      font-size: 16px;
      font-weight: normal; }
    .accordion-product .accordion__head:after {
      content: '';
      position: absolute;
      top: calc(50% - 3px);
      right: 17px;
      transform: translateY(-50%) rotate(45deg);
      width: 5px;
      height: 5px;
      border-width: 0 1px 1px 0;
      border-color: #152342;
      border-style: solid;
      transition: transform 0.4s, top 0.4s;
      /*Mobile*/ }
      @media (max-width: 1024px) {
        .accordion-product .accordion__head:after {
          transform: rotate(-45deg); } }
    .accordion-product .accordion__head a {
      text-decoration: none; }
      .accordion-product .accordion__head a:hover {
        text-decoration: none; }
  .accordion-product .accordion__body {
    display: none;
    padding: 27px 15px 0;
    background: #f2f2f2; }
    .accordion-product .accordion__body h3 {
      margin-top: 20px;
      font-size: 15px; }
      .accordion-product .accordion__body h3:first-child {
        margin-top: 0; }
    .accordion-product .accordion__body p {
      margin-bottom: 7px; }
      .accordion-product .accordion__body p:last-child {
        margin-bottom: 0; }
    .accordion-product .accordion__body--text-grey p {
      color: #999; }
    .accordion-product .accordion__body--text-grey strong {
      color: #152342; }
    .accordion-product .accordion__body--text-grey .btn-text {
      display: inline-block;
      margin-top: 10px;
      font-size: 12px;
      color: #999; }
  .accordion-product .accordion__entry {
    padding-bottom: 25px; }
  .accordion-product .accordion__section:last-child .accordion__head a {
    border-bottom-color: transparent; }
  .accordion-product .expanded .accordion__head a {
    border-bottom-color: transparent; }
  .accordion-product .expanded .accordion__head:after {
    transform: translateY(-50%) rotate(45deg); }

/* ------------------------------------------------------------ *\
    Banner
\* ------------------------------------------------------------ */
.banner {
  position: relative; }
  .banner:before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    background: rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s, visibility 0.4s;
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .banner:before {
        opacity: 1;
        visibility: visible; } }
  .banner .banner__content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 5;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 15px;
    color: #fff;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s, visibility 0.4s;
    /*Mobile*/ }
    @media (max-width: 1024px) {
      .banner .banner__content {
        opacity: 1;
        visibility: visible; } }
    .banner .banner__content h3 {
      margin-bottom: 23px;
      font-size: 30px;
      color: #fff;
      text-transform: uppercase;
      /*Mobile*/ }
      @media (max-width: 1024px) {
        .banner .banner__content h3 {
          margin-bottom: 22px; } }
      .banner .banner__content h3:last-child {
        margin-bottom: 0; }
  .banner .banner__entry {
    transform: translateY(4px);
    width: 100%; }
  .banner .btn-text i {
    margin-left: 6px; }
  .banner:hover:before {
    opacity: 1;
    visibility: visible; }
  .banner:hover .banner__content {
    opacity: 1;
    visibility: visible; }

/* ------------------------------------------------------------ *\
    Banner Seconday
\* ------------------------------------------------------------ */
.banner-secondary .banner__inner {
  position: relative; }

.banner-secondary .banner__content {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 37px 22px; }
  .banner-secondary .banner__content h4 {
    font-size: 21px;
    line-height: 1.2;
    text-transform: uppercase; }
  .banner-secondary .banner__content .btn-text {
    font-size: 12px; }
    .banner-secondary .banner__content .btn-text i {
      margin-left: 6px; }

/* ------------------------------------------------------------ *\
    Banner Tertiary
\* ------------------------------------------------------------ */
.banner-tertiary .banner__inner {
  position: relative; }

.banner-tertiary .banner__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  padding: 45px 20px 20px;
  color: #fff;
  text-align: right; }
  .banner-tertiary .banner__content h3 {
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 1.2;
    color: #fff;
    text-transform: none; }
    .banner-tertiary .banner__content h3:last-child {
      margin-bottom: 0; }
  .banner-tertiary .banner__content .btn-text {
    margin-right: -2px;
    font-size: 12px; }
    .banner-tertiary .banner__content .btn-text i {
      margin-left: 6px; }

