/* ------------------------------------------------------------ *\
	Section
\* ------------------------------------------------------------ */

.section {
}

/* ------------------------------------------------------------ *\
	Section Image
\* ------------------------------------------------------------ */

.section-image {
	padding: 24px 0;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 20px 0;
	}

	img {
		display: block; 
		margin-right: auto; 
		margin-left: auto; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0;
		}
	}

	h2 {
		margin-bottom: 26px; 
		font-size: 40px; 
		text-transform: none; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 12px; 
			font-size: 30px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__head {
		position: relative; 
		margin-bottom: 54px; 
		text-align: center; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}

		.breadcrumbs {
			position: absolute; 
			top: 19px; 
			left: 0; 

			/*Mobile*/
			@include breakpoint-mobile {
				position: static; 
				margin-bottom: 4px; 

				&:last-child {
					margin-bottom: 0; 
				}
			}
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__body {
		padding-top: 21px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding-top: 0; 
		}
	}

	.section__title {
		padding: 0 200px;
		font-size: 40px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0; 
			margin-bottom: 20px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}
	}

	.section__entry {
		max-width: 207px;
		margin-bottom: 22px; 
		line-height: 1.22; 
		color: $c-grey-dark;
		
		&:last-child {
			margin-bottom: 0; 
		}
	}

	.cols {
		+ .cols {
			margin-top: 8px; 
		}
	}

	.col {
		/*Mobile*/
		@include breakpoint-mobile {
			+ .col {
				margin-top: 16px; 
			}

			&--size1 {
				order: 1;
				max-width: 55.7%;
				flex: 0 0 55.7%;
				margin-bottom: 15px; 
			}
		}

		&--size2 {
			padding-left: 12px; 
			margin-bottom: 13px; 

			/*Mobile*/
			@include breakpoint-mobile {
				order: 2;
				padding-left: 15px; 
				margin-bottom: 0; 
			}
		}

		&--1of5 {
			margin-top: 12px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-top: 0; 
			}
		}

		.cols {
			margin-right: -7px; 
			margin-left: -7px; 
		}

		.col {
			padding: 0 6px;
		}
	}

	.btn {
		min-width: 102px;
		padding: 0 15px;
		border-width: 1px;
		line-height: 29px;

		i:not(:first-child) {
			margin-left: 3px; 
		} 
	}

	&--swap {
		text-align: right; 

		/*Mobile*/
		@include breakpoint-mobile {
			text-align: center; 
		}

		h2 {
			margin-bottom: 38px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 15px; 
			}
		}

		.section__entry {
			margin-bottom: 37px; 
			margin-left: auto; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin: 0 auto 9px;
			}
		}

		.col--size2 {
			padding-left: 15px; 
			margin-bottom: 30px; 
		}
	}

	&--lg {
		padding-top: 28px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding-top: 7px; 
			text-align: center; 
		}

		h2 {
			margin-bottom: 40px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 9px; 

				&:last-child {
					margin-bottom: 0; 
				}
			}
		}

		.section__body {
			padding-top: 0; 

			> .cols {
				margin: 0;
			}
		}

		.col--1of5,
		.col--4of5,
		.col--size5 {
			padding: 0;
		}

		.section__entry {
			margin-bottom: 41px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin: 0 auto 11px;
			}
		}

		.col {
			&--1of5 {
				/*Mobile*/
				@include breakpoint-mobile {
					order: 2;
				}
			}

			&--4of5 {
				/*Mobile*/
				@include breakpoint-mobile {
					order: 1;
					margin-top: 0 !important; 
					margin-bottom: 16px; 
				}
			}
		}
	}

	&--secondary {
		padding-top: 25px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 3px 0 17px;
		}

		h2 {
			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 11px; 

				br {
					display: none; 
				}
			}
		}

		.section__body {
			padding-top: 0; 
		}

		.col {
			&--2of3 {
				transform: translateX(17px); 

				/*Mobile*/
				@include breakpoint-mobile {
					transform: none;
					max-width: 53.5%;
					flex: 0 0 53.5%;
				}

				img {
					margin-bottom: 10px; 

					/*Mobile*/
					@include breakpoint-mobile {
						margin: 0 auto;
					}

					&:last-child {
						margin-bottom: 0; 
					}
				}
			}

			&--size4 {
				margin-top: 21px; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-top: 10px !important; 
				}
			}
		}


		.section__entry {
			padding-right: 6px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				padding-right: 0; 
			}

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 12px; 
			}
		}
	}

	&--tertiary {
		padding-top: 36px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 10px 0;
		}

		h2 {
			margin-bottom: 30px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 6px; 
			}
		}

		.col {
			&--1of5 {
				padding-right: 0; 
				margin-top: 1px; 

				/*Small Desktop*/
				@include breakpoint-small-desktop {
					padding-right: 15px; 
				}

				/*Mobile*/
				@include breakpoint-mobile {
					order: 2;
				}
			}

			&--size1 {
				/*Mobile*/
				@include breakpoint-mobile {
					order: 1;
					max-width: 74.7%;
					flex: 0 0 74.7%;
					margin-top: 0 !important; 
					margin-bottom: 7px; 
				}
			}
		}

		.section__entry {
			padding-left: 6px; 
			margin-right: -10px; 
			margin-bottom: 47px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				padding-left: 0; 
				margin-right: 0; 
			}

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 11px; 
			}
		}

		.btn {
			margin-left: 7px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				margin-left: 0; 
			}
		}
	}

	&--quaternary {
		padding-top: 31px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 15px 0 13px;
		}

		h2 {
			margin-bottom: 43px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 7px; 
			}
		}

		.col {
			&--1of5 {
				padding-right: 0; 
				margin-bottom: 40px; 

				/*Small Desktop*/
				@include breakpoint-small-desktop {
					margin-bottom: 0; 
				}
			}

			&--size5 {
				/*Mobile*/
				@include breakpoint-mobile {
					margin-top: 0 !important; 
					margin-bottom: 8px; 

					img {
						margin: 0 auto;
					}
				}
			}
		}

		.section__entry {
			padding-left: 6px; 
			margin-right: -10px; 
			margin-bottom: 37px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				margin-right: 0; 
			}

			/*Mobile*/
			@include breakpoint-mobile {
				margin: 0 auto 14px;
			}
		}

		.btn {
			margin-left: 7px; 

			/*Small Desktop*/
			@include breakpoint-small-desktop {
				margin-left: 0; 
			}
		}
	}

	&--quinary {
		padding: 35px 0 26px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 10px 0;
		}

		h2 {
			margin-bottom: 45px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 3px; 
			}
		}

		.col {
			&--size6 {
				/*Mobile*/
				@include breakpoint-mobile {
					img {
						margin: 0 0 0 auto;
					}
				}
			}

			&--size7 {
				padding-right: 25px; 
				padding-left: 0;
				margin: 0 auto 10px;

				/*Small Desktop*/
				@include breakpoint-small-desktop {
					padding-right: 15px; 
					padding-left: 15px; 
					margin-top: 10px !important; 
					text-align: right; 

					&:last-child {
						margin-bottom: 0; 
					}
				}
			}
		}

		.section__body {
			padding-top: 0; 
		}

		.section__entry {
			max-width: 100%;
			margin-bottom: 42px; 

			/*Mobile*/
			@include breakpoint-mobile {
				max-width: 250px;
				margin: 0 0 7px auto;
			}
		}
	}

	&--senary {
		padding: 35px 0 25px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 17px 0 10px;
		}

		h2 {
			margin-bottom: 41px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 9px; 
			}
		}

		.col {
			/*Mobile*/
			@include breakpoint-mobile {
				&--size1 {
					max-width: 100%;
					flex: 0 0 100%;
					margin-bottom: 11px; 

					img {
						margin: 0 auto;
					}
				}
			}

			&--size2 {
				padding-right: 11px; 
				margin-top: 33px; 
				margin-bottom: 0; 

				/*Small Desktop*/
				@include breakpoint-small-desktop {
					padding-right: 15px; 
				}

				/*Mobile*/
				@include breakpoint-mobile {
					margin-top: 0 !important; 
				}
			}
		}

		.list-links-secondary {
			margin-top: 71px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-top: 21px; 
			}
		}

		.section__body {
			padding-top: 0; 
		}

		.section__entry {
			margin-bottom: 42px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 9px; 
			}
		}
	}

	&--septenary {
		padding-top: 0; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 31px 0 15px;
		}

		h2 {
			margin-bottom: 30px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 9px; 
			}
		}

		.section__head {
			margin-bottom: -101px; 
			z-index: -1;
		}

		.section__title {
			padding: 0;
			font-size: 150px; 
		}

		.section__entry {
			margin-bottom: 34px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin: 0 auto 13px;
			}
		}

		.col {
			/*Mobile*/
			@include breakpoint-mobile {
				&--size1 {
					max-width: 100%; 
					flex: 0 0 100%;
					margin-top: 0 !important; 
					margin-bottom: 9px; 

					img {
						margin: 0 auto;
					}
				}
			}

			&--1of3 {
				margin: 95px 0 80px;

				/*Mobile*/
				@include breakpoint-mobile {
					order: 2; 
					margin: 0;
					text-align: center; 
				}
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Section Image Full
\* ------------------------------------------------------------ */

.section-image-full {
	padding: 26px 0 44px;
	color: $c-white;
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 11px 0;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	a {
		color: $c-white;
	}

	.btn-text {
		font-size: 12px; 
	}

	.section__image {
		position: relative; 
		margin-bottom: 55px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 17px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}

		&-content {
			@include center;
			padding: 50px;

			/*Mobile*/
			@include breakpoint-mobile {
				padding: 30px 20px 16px;
			}
		}
	}

	.section__title {
		margin-bottom: 34px; 
		font-size: 50px; 

		/*Mobile*/
		@include breakpoint-mobile {
			font-size: 30px; 
			text-transform: none; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__content {
		line-height: 1.18; 
		color: $c-grey-dark;

		p {
			margin-bottom: 0; 
		}
	}

	&--secondary {
		padding: 42px 0;
		text-align: right; 

		.section__image {
			&-content {
				right: 0; 
				transform: translateY(-50%);
				padding: 41px 41px 35px;
			}
		}

		.section__title {
			font-size: 80px; 
			text-transform: none; 
		}
	}
}

/* ------------------------------------------------------------ *\
	section Banner
\* ------------------------------------------------------------ */

.section-banner {
	/*Mobile*/
	@include breakpoint-mobile {
		padding-top: 8px; 
	}

	.section__body {
		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 -30px;
		}
	}

	.cols {
		margin: 0 -7px;

		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 auto;
		}
	}

	.col {
		padding: 0 7px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0 15px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Section Products
\* ------------------------------------------------------------ */

.section-products {
	padding: 62px 0 72px;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 38px 0;
	}

	.products {
		margin-bottom: 35px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 45px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.cols {
		margin: 66px -50px 16px;

		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 0 40px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		&:first-child {
			margin-top: 0; 
		}
	}

	.col {
		padding: 0 50px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0; 
			margin-bottom: 45px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		.product {
			margin-bottom: 36px;

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 45px; 
			}

			&:last-child {
				margin-bottom: 0; 
			} 
		}
	}

	.breadcrumbs {
		position: absolute; 
		top: 16px; 
		left: 0; 

		/*Mobile*/
		@include breakpoint-mobile {
			position: static; 
			margin-bottom: 5px; 
		}
	}

	.section__head {
		position: relative; 
		margin-bottom: 38px; 
		text-align: center; 
		z-index: 5;

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 18px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__title {
		padding: 0 200px;
		margin-bottom: 30px; 
		font-size: 30px; 

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0;
			margin-bottom: 29px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__body {
		display: flex; 
		flex-wrap: wrap;
	}

	.sidebar {
		padding-top: 3px; 

		/*Mobile*/
		@include breakpoint-mobile {
			position: absolute; 
			top: 0; 
			right: 0; 
			left: 0; 
			z-index: 200;
			padding: 25px 15px 23px;
			background: $c-grey;
			opacity: 0; 
			visibility: hidden; 
			transition: opacity $t-duration, visibility $t-duration;

			.nicescroll-rails {
				z-index: 55 !important;
				transform: translateX(-6px);
			}

			&.active {
				opacity: 1; 
				visibility: visible; 
			}
		}

		h3 {
			margin-bottom: 20px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		.widgets {
			/*Mobile*/
			@include breakpoint-mobile {
				margin: 0 -15px;
				text-align: center; 

				.widget {
					margin-bottom: 0; 
					text-align: left; 

					&:last-child {
						.widget__body {
							text-align: center; 

							&:after {
								display: none; 
							}
						}
					}

					&.hover {
						.widget__title {
							font-weight: bold; 

							&--alt {
								padding-bottom: 0; 
								border-bottom: 0; 
							}
						}
					}

					.btn-text {
						display: inline-block; 
						margin-bottom: 15px; 
						margin-left: 25px; 
					}
				}

				.widget__title {
					position: relative; 
					padding: 21px 0 23px;
					border-bottom: 1px solid #bdbdbd; 
					margin: 0 15px;
					font-size: 15px; 
					font-weight: normal;
					transition: padding $t-duration; 

					&:after {
						content: '';
					    position: absolute;
					    top: 27px;
					    right: 2px;
					    transform: rotate(45deg);
					    width: 5px;
					    height: 5px;
					    border-width: 0 1px 1px 0;
					    border-color: #152342;
					    border-style: solid;
					    transition: transform $t-duration, top $t-duration;
					}
				}

				.checkbox {
					label {
						&:before {
							top: 1px; 
						}

						&:after {
							top: 3px; 
						}
					}
				}

				.widget__body {
					position: relative; 
					display: none; 

					&:after {
						content: ''; 
						position: absolute; 
						right: 15px; 
						bottom: 0; 
						left: 15px; 
						height: 1px; 
						background: #bdbdbd;
					}

					> .list-checkboxes {
						padding-top: 15px; 
						padding-bottom: 21px; 

						li {
							margin-bottom: 11px; 

							&:last-child {
								margin-bottom: 0; 
							}
						}

						.checkbox {
							padding: 0px 15px;

							label {
								padding-left: 28px; 
								color: $c-blue-dark;
							}

							input:checked + label {
								font-weight: normal; 
							}
						}
					}
				}

				.list-links-tertiary {
					li {
						margin-bottom: 0; 
					}

					a {
						display: block; 
						padding: 15px 5px;
						border-bottom: 1px solid #bdbdbd; 
						margin: 0 20px;
						color: $c-blue-dark;
					}
				}

				.accordion-filter {
					position: relative; 
					padding: 10px 0 0;
					transition: padding $t-duration;
				}

				.accordion__head {
					display: flex; 
					justify-content: space-between;
					align-items: center;

					&:after {
						position: static; 
						width: 5px; 
						height: 5px; 
						transform: translateX(-27px) rotate(-45deg); 
						transition: transform $t-duration;
					}

					a {
						display: block; 
						width: calc(100% - 10px); 
						padding: 17px 5px 15px;
						border-bottom: 1px solid #bdbdbd; 
						margin: 0 20px;
						color: $c-blue-dark;
					}
				}

				.accordion__body {
					position: absolute; 
					top: 0; 
					right: 15px; 
					bottom: 0; 
					left: 50%;
					z-index: 5;
					transform: translateX(120%); 
					padding: 5px 7px 5px 15px;
					background: $c-white;
					transition: transform $t-duration;

					ul {
						padding-right: 14px; 
					}

					.checkbox {
						padding: 18px 0 12px;
					}

					li {
						padding-left: 2px; 
						border-bottom: 1px solid #bdbdbd; 
						margin-bottom: 0; 

						&:last-child {
							border-bottom: 0; 
						}
					}
				}

				.accordion__section {
					width: 100%; 
					margin-bottom: 0; 
					transition: width $t-duration;
				}

				.expanded {
					.widget__title {
						font-weight: bold; 

						&:after {
							transform: rotate(225deg);
						}
					}

					.accordion__section {
						width: 50%; 
					}

					.accordion__head {
						&:after {
							transform: translateX(-8px) rotate(-45deg);
						}

						a {
							margin: 0 0 0 11px;
							border-bottom-color: transparent; 
						}
					}

					.hover {
						background: $c-white;

						.accordion__head {
							&:after {
								transform: translateX(-8px) rotate(135deg);
							}
						}

						.accordion__body {
							transform: translateX(0);
						}
					}
				}
			}
		}

		.list-tags {
			margin-bottom: 35px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 10px; 
