/* ------------------------------------------------------------ *\
	Section Background
\* ------------------------------------------------------------ */

.section-background {
	position: relative; 
	display: table; 
	width: 100%; 
	height: 525px; 
	padding: 107px 0 100px;
	background: url(../images/temp/section-bg.jpg) center no-repeat; 
	background-size: cover;
	color: $c-white;
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		height: auto; 
		padding: 85px 0 73px;
	}

	&:before {
		@include full;
		content: ''; 
		background: #0066ad;
		mix-blend-mode: multiply;
		pointer-events: none;
	}

	.section__inner {
		display: table-cell; 
		vertical-align: middle; 
	}

	.shell {
		position: relative; 
		z-index: 5;
	}

	.logo {
		display: inline-block; 
		max-width: 205px;
		margin-bottom: 36px; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: block; 
			max-width: 120px;
			margin: 0 auto 40px;
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__entry {
		max-width: 608px;
		margin: 0 auto 42px;

		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}
	}

	.btn-text {
		i {
			margin-left: 7px; 
		}
	}
}

/* ------------------------------------------------------------ *\
	Section Subscribe
\* ------------------------------------------------------------ */

.section-subscribe {
	padding: 42px 0 38px;
	background: $c-grey;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 32px 0 51px;
		text-align: center; 
	}

	.shell {
		display: flex; 
		align-items: center;
		flex-wrap: wrap;
	}

	.section__head {
		width: 402px; 
		padding-right: 40px;
		font-size: 11px; 

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
			padding-right: 0; 
			margin-bottom: 38px; 
			font-size: 10px; 

			&:last-child {
				margin-bottom: 0; 
			}
		}

		h2 {
			margin-bottom: 4px; 
			font-size: 37px; 
			line-height: 1; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 5px; 
				font-size: 33px; 
			}
		}

		h5 {
			margin-bottom: 2px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-bottom: 3px; 
				font-size: 10px; 
			}
		}

		h1,
		h3,
		h4,
		h6,
		p {
			margin-bottom: 0; 
		}
	}

	.section__body {
		width: calc(100% - 402px); 

		/*Mobile*/
		@include breakpoint-mobile {
			width: 100%; 
		}
	}
}

/* ------------------------------------------------------------ *\
	Section Featured
\* ------------------------------------------------------------ */

.section-featured {
	padding: 47px 0;

