/* ------------------------------------------------------------ *\
	Section Tales
\* ------------------------------------------------------------ */

.section-tales {
	padding: 49px 0;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 10px 0 49px;
	}

	.shell {
		display: flex;
		flex-wrap: wrap;
		max-width: (1100px + $shell-gutter * 2);
	}

	.section__title {
		margin-bottom: 24px; 
		font-size: 98px; 
		font-weight: bold; 
		text-transform: uppercase; 

		/*Mobile*/
