/* ------------------------------------------------------------ *\
	Section Instagram
\* ------------------------------------------------------------ */

.section-instagram {
	position: relative; 
	padding: 42px 0 31px;
	margin-bottom: 49px; 
	font-size: 14px; 
	text-align: center; 

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 36px 0 40px;
		margin-bottom: 0; 
		font-size: 16px; 
	}

	&:before {
		content: ''; 
		position: absolute; 
		top: 0; 
		bottom: 0; 
		left: 50%; 
		transform: translateX(-50%); 
		z-index: -1; 
		max-width: 816px;
		width: 100%; 
		background: $c-blue-dark;

		/*Mobile*/
		@include breakpoint-mobile {
			max-width: 700px;
		}

		@media (max-width: 767px) {
			right: 15px; 
			left: 15px; 
			width: auto; 
			max-width: auto;
			transform: translateX(0); 
		}
	
}	
	&:last-child {
		margin-bottom: 0; 
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	a {
		color: $c-white;
	}

	.section__head {
		margin-bottom: 50px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 44px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__body {
		/*Mobile*/
		@include breakpoint-mobile {
			margin: 0 -15px;
			overflow-x: auto; 
			overflow-y: hidden; 
		}
	}

	.section__title {
		font-size: 30px; 
		text-transform: none; 
	}

	.section__foot {
		margin-top: 38px; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-top: 39px; 
		}

		&:first-child {
			margin-top: 0; 
		}
	}
}

