/* ------------------------------------------------------------ *\
	Section Related
\* ------------------------------------------------------------ */

.section-related {
	padding: 8px 0 56px;

	/*Mobile*/
	@include breakpoint-mobile {
		padding: 36px 0 36px;
	}

	.section__head {
		margin-bottom: 23px; 
		text-align: center; 

		/*Mobile*/
		@include breakpoint-mobile {
			margin-bottom: 30px; 
		}

		&:last-child {
			margin-bottom: 0; 
		}
	}

	.section__title {
		font-size: 30px; 
		text-transform: none; 
	}

	.section__body {
		padding: 0 102px;

		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0;
		}
	}

	.product {
		max-width: 205px;

		/*Mobile*/
		@include breakpoint-mobile {
			.product__content {
				h3 {
					margin-bottom: 3px; 
				}

				p {
					margin-bottom: 4px; 

					&:last-child {
						margin-bottom: 0; 
					}
				}
			}

			.product__wishlist {
				top: 8px; 
				left: 7px; 

				.ico-heart {
					width: 14px; 
					height: 11px; 
				}				

				&:hover {
					text-decoration: none; 

					i {
						background-image: url(../images/heart-dark@2x.png);

						/*Mobile*/
						@include breakpoint-mobile {
							background-image: url(../images/heart@2x.png);
						}
					}

					span {
						display: inline-block; 

						/*Mobile*/
						@include breakpoint-mobile {
							display: none; 
						}
					}
				}
			}

			.list-gallery {
				max-width: 208px;
				padding: 0 10px;
				margin-bottom: 7px; 

				li {
					padding: 0 3px;
				}

				.slick-arrow {
					width: 6px; 
					height: 6px; 
				}
			}

			.list-price {
				margin-bottom: 1px; 
			}

			.btn-text {
			    height: auto;
			    padding: 0;
			    border: 0;
			    border-radius: 0;
			    background: none;
			    font-size: 10px;
			    line-height: 1.2;
			    color: #b11730;
			}
		}
	}

	.cols {
		/*Mobile*/
		@include breakpoint-mobile {
			display: block; 
		}
	}

	.col {
		/*Mobile*/
		@include breakpoint-mobile {
			padding: 0;
		}

		.product {
			margin: 0 auto;
		}

		&:nth-child(3n-2) {
			.product {
				margin-left: 0; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-left: auto; 
				}
			}
		}

		&:nth-child(3n) {
			.product {
				margin-right: 0; 

				/*Mobile*/
				@include breakpoint-mobile {
					margin-right: auto; 
				}
			}
		}
	}
}

