/* ------------------------------------------------------------ *\
	Btn
\* ------------------------------------------------------------ */

.btn {
	position: relative;
	display: inline-block;
	min-width: 208px;
	height: 31px; 
	padding: 0 20px;
	border: 2px solid $c-red; 
	border-radius: 15px; 
	background: transparent;
	font-size: 12px;
	line-height: 27px;
	color: $c-red;
	font-weight: bold; 
	text-align: center;
	text-decoration: none; 
	cursor: pointer;
	appearance: none;
	transition: border $t-duration, background $t-duration, color $t-duration;

	/*Mobile*/
	@include breakpoint-mobile {
		min-width: 150px;
		padding: 0 18px;
	}

	i {
		vertical-align: middle; 

		&:not(:first-child) {
			margin-left: 5px; 
		}
	}
	
	&:hover {
		background: $c-red;
		color: $c-white;
		text-decoration: none; 
	}

	&--md {
		min-width: 60px;
		height: 39px; 
		padding: 0 12px;
		border-radius: 20px; 
		line-height: 35px; 

		i {
			transform: translateY(-1px); 

			&:not(:first-child) {
				margin-left: 8px; 
			}
		}
	}

	&--sm {
		min-width: 103px;
		height: 20px; 
		padding: 0 15px;
		border-radius: 10px; 
		font-size: 10px; 
		line-height: 16px;

		i {
			&:not(:first-child) {
				margin-left: 2px;
			}
		}
	}

	&--xs {
		min-width: 83px;
		height: 20px; 
		padding: 0 10px;
		border-radius: 10px; 
		font-size: 10px; 
		line-height: 16px;

		i {
			&:not(:first-child) {
				margin-left: 2px;
			}
		}
	}

	&--red {
		background: $c-red;
		color: $c-white;

		&:hover {
			background: transparent;
			color: $c-red;
		}
	}

	&--grey-transparent {
		border-color: $c-grey-dark;
		color: $c-blue-dark;

		&:hover {
			background: $c-grey-dark;
			color: $c-blue-dark;
		}
	}

	&-text {
		font-size: 11px; 
		line-height: 1.27; 
		font-weight: bold; 
		text-transform: uppercase; 

		i {
			margin-right: 3px; 
			margin-left: 3px; 

			/*Mobile*/
			@include breakpoint-mobile {
				margin-left: 10px; 
			}

			&:first-child {
				margin-left: 0; 
			}

			&:last-child {
				margin-right: 0; 
			}
		}
	}

	&-profile,
	&-cart,
	&-search {
		font-size: 10px; 
		font-weight: bold; 
		text-transform: uppercase; 

		span,
		i {
			display: inline-block; 
			vertical-align: middle; 
		}

		i {
			transform: translateY(-2px); 
			margin-right: 9px;

			/*Mobile*/
			@include breakpoint-mobile {
				transform: translateY(-1px); 
				margin-right: 3px; 
			}

			&:last-child {
				margin-right: 0; 
			} 
		}
		
		&:hover {
			text-decoration: none; 
		}
	}

	&-filter {
		margin-right: 8px; 
		font-weight: bold; 

		span,
		i {
			display: inline-block; 
			vertical-align: middle; 
		}

		i {
			margin-right: 8px; 
		}

		&:hover {
			text-decoration: none; 
		}
	}

	&-search {
		position: relative; 

		/*Mobile*/
		@include breakpoint-mobile {
			order: 3; 
			width: 100%; 
			padding-bottom: 6px; 
			margin: 14px 0 0 3px;
			font-weight: bold; 
		}

		&:before {
			content: ''; 
			position: absolute; 
			right: -6px; 
			bottom: -16px; 
			left: -10px; 
			height: 2px; 
			background: transparent;
			transition: background $t-duration;
			pointer-events: none;


			/*Mobile*/
			@include breakpoint-mobile {
				right: 0; 
				bottom: 0; 
				left: 0; 
				height: 1px; 
				background: $c-grey-dark;
			}
		}

		i {
			/*Mobile*/
			@include breakpoint-mobile {
				margin-right: 10px; 
			}
		}

		&.active {
			&:before {
				background: #ae1232;

				/*Mobile*/
				@include breakpoint-mobile {
					background: $c-grey-dark;
				}
			}
		}
	}

	&-cart {
		position: relative; 

		/*Mobile*/
		@include breakpoint-mobile {
			font-size: 12px; 
		}

		&:before {
			content: ''; 
			position: absolute; 
			right: -0; 
			bottom: -18px; 
			left: 0; 
			height: 2px; 
			background: transparent;
			transition: background $t-duration;
			pointer-events: none;

			/*Mobile*/
			@include breakpoint-mobile {
				bottom: -4px; 
			}
		}

		&.active {
			&:before {
				background: #ae1232;
			}
		}
	}

	&-profile {
		/*Mobile*/
		@include breakpoint-mobile {
			display: none; 
		}
	}

	&-menu { 
		position: relative; 
		z-index: 100; 
		display: none;
		width: 17px; 
		height: 17px; 
		margin-top: -2px; 

		/*Mobile*/
		@include breakpoint-mobile {
			display: block; 
		}	

		*,
		*:before,
		*:after {
			transition: background .4s, margin .4s, transform .4s; 
		}

		span {
			content: ''; 
			position: absolute; 
			left: 0; 
			right: 0; 
			top: 50%; 
			margin-top: -1px; 
			height: 1px; 
			background-color: $c-blue-dark; 

			&:before,
			&:after {
				content: ''; 
				position: absolute; 
				left: 0; 
				right: 0; 
				top: 50%; 
				margin-top: -1px; 
				height: 1px; 
				background-color: $c-blue-dark; 
			}

			&:before {
				margin-top: -5px; 
			}

			&:after {
				margin-top: 5px; 
			}
		}
	}
}

body {
	&.show-nav-main {
		.btn {
			&-menu {
				span { 
					background-color: transparent; 

					&:before,
					&:after { 
						margin-top: -1px; 
					}

					&:before { 
						transform: rotate(45deg); 
					}

					&:after { 
						transform: rotate(-45deg); 
					}
				}
			}
		}
	}
}