/* ------------------------------------------------------------ *\
	Sidebar
\* ------------------------------------------------------------ */

.sidebar {
	width: 203px; 
	padding-right: 20px; 

	/*Mobile*/
	@include breakpoint-mobile {
		width: 100%; 
		padding-right: 0; 
	}

	.btn-text {
		font-size: 12px; 
		color: #ae1232;

		&--alt {
			display: inline-block; 
			margin-top: 8px; 
			color: $c-grey-dark;

			/*Mobile*/
			@include breakpoint-mobile {
				min-width: 100px;
				margin-bottom: 0; 
				height: 20px; 
				padding: 0 10px;
				margin-top: 27px; 
				border: 2px solid $c-grey-dark;
				border-radius: 10px; 
				font-size: 11px; 
				line-height: 15px; 
				text-align: center; 

				i {
					margin-left: 4px; 
				}

				&:hover {
					background: $c-grey-dark;
					color: $c-white;
					text-decoration: none; 
				}
			}
		}
	}
}
