/* ------------------------------------------------------------ *\
    Banner
\* ------------------------------------------------------------ */

.banner {
    position: relative; 

    &:before {
        @include full;
        content: ''; 
        background: rgba(0,0,0,.3);
        opacity: 0; 
        visibility: hidden; 
        transition: opacity $t-duration, visibility $t-duration;

        /*Mobile*/
        @include breakpoint-mobile {
            opacity: 1; 
            visibility: visible; 
        }
    }

    .banner__content {
        @include full;
        z-index: 5;
        display: flex;
        align-items: center;
        width: 100%; 
        padding: 20px 15px;
        color: $c-white;
        text-align: center; 
        opacity: 0; 
        visibility: hidden; 
        transition: opacity $t-duration, visibility $t-duration;

        /*Mobile*/
        @include breakpoint-mobile {
            opacity: 1; 
            visibility: visible; 
        }

        h3 {
            margin-bottom: 23px; 
            font-size: 30px; 
            color: $c-white;
            text-transform: uppercase; 

            /*Mobile*/
            @include breakpoint-mobile {
                margin-bottom: 22px; 
            }

            &:last-child {
                margin-bottom: 0; 
            }
        }
    }

    .banner__entry {
        transform: translateY(4px); 
        width: 100%; 
    }

    .btn-text {
        i {
            margin-left: 6px; 
        }
    }

    &:hover {
        &:before {
            opacity: 1; 
            visibility: visible; 
        }

        .banner__content {
            opacity: 1; 
            visibility: visible; 
        }
    }
}

/* ------------------------------------------------------------ *\
    Banner Seconday
\* ------------------------------------------------------------ */

.banner-secondary {
    .banner__inner {
        position: relative; 
    }

    .banner__content {
        position: absolute; 
        right: 0; 
        bottom: 0; 
        left: 0; 
        padding: 37px 22px;

        h4 {
            font-size: 21px; 
            line-height: 1.2; 
            text-transform: uppercase; 
        }

        .btn-text {
            font-size: 12px; 

            i {
                margin-left: 6px; 
            }
        }
    }
}

/* ------------------------------------------------------------ *\
    Banner Tertiary
\* ------------------------------------------------------------ */

.banner-tertiary {
    .banner__inner {
        position: relative; 
    }

    .banner__content {
        @include center;
        width: 100%; 
        padding: 45px 20px 20px;
        color: $c-white;
        text-align: right; 

        h3 {
            margin-bottom: 15px; 
            font-size: 30px; 
            line-height: 1.2; 
            color: $c-white;
            text-transform: none; 

            &:last-child {
                margin-bottom: 0; 
            }
        }

        .btn-text {
            margin-right: -2px; 
            font-size: 12px; 

            i {
                margin-left: 6px; 
            }
        }
    }
}

